import { useState, useMemo, useEffect } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import LogicElementUtil from "../../factory/util/logicElementUtil";
import LogicUtil from "../../factory/util/logicUtil";
import PreviewUtil from "../previewUtil";

const PreviewLogicInputForm = (props: {
    logicWrap: LogicUtil.WrapElement;
    setSendingValueMap: (sendingValueMap: (() => PreviewUtil.ValueMap[]) | null) => void;
}) => {

    const [formList, setFormList] = useState<FormUtil.CheckableValue[]>([]);

    const argumentList: string[] = useMemo(() => {
        let list: string[] = [];
        const argWrap = LogicElementUtil.getInnerWrap(props.logicWrap, 'argument-mng');
        if (argWrap != null) {
            list = (argWrap.data as LogicUtil.NodeArgumentManager).args.map(arg => {
                const argItem = arg.data as LogicUtil.NodeArgumentItem;
                return argItem.name;
            });
        }
        return list;
    }, []);

    useEffect(() => {
        setFormList(argumentList.map(arg => {
            return { value: arg, errors: [] };
        }));
    }, []);


    useEffect(() => {
        if (formList.find(form => form.errors.length > 0) == undefined) {
            props.setSendingValueMap(() => {
                return formList.map((form, i) => {
                    return { name: argumentList.map(arg => arg)[i], value: Number(form.value) }
                });
            })
        } else {
            props.setSendingValueMap(null);
        }
    }, [formList]);

    return <>{
        formList.map((form, i) => {
            const argument = argumentList[i];
            return (<FormUtil.FormRecord
                key={i}
                titleLabel={argument}
                labelWidth={250}
                jsx={
                    <FormUtil.TextField
                        width={312}
                        isNumber
                        checkable={form}
                        setCheckable={() => {
                            setFormList(formList.slice());
                        }}
                        validates={[
                            {
                                checker: (value) => ValidateUtil.checkRequired(value),
                                errorType: "required"
                            }
                        ]}
                    />
                }
            />);
        })
    }</>
}

export default PreviewLogicInputForm;