import { assert } from "console";
import { Store } from "../../../../redux/store/store";
import FormUtil from "../../../../utils/form/formUtiil";
import TreeUtil from "../../../../utils/treeUtil";
import ConfirmDialog from "../../../dialog/confirmDialog";
import Particle from "../../../graphic/particle";
import { LaunchDef } from "../../launcher/launcherTab";
import PreviewUtil from "../../preview/previewUtil";
import LogicUtil from "./logicUtil";
import ParamUtil from "./paramUtil";


namespace LogicElementUtil {

    export const getInnerNode = (
        node: TreeUtil.ElementNode, ...typeList: LogicUtil.NodeType[]
    ): TreeUtil.ElementNode | null => {

        let cur: TreeUtil.ElementNode | null = node;
        typeList.some(type => {
            if (cur == null) return 1;
            const target = cur.children.find(child => (child.data as LogicUtil.WrapElement).type === type);
            if (target == undefined) cur = null;
            else cur = target;
        });
        return cur;
    }

    export const getInnerNodeFixed = (
        node: TreeUtil.ElementNode, ...typeList: LogicUtil.NodeType[]
    ): TreeUtil.ElementNode => {
        const innerNode = getInnerNode(node, ...typeList);
        if (innerNode == null) throw new Error('[innerNode] is null.');
        return innerNode as TreeUtil.ElementNode;
    }

    export const getInnerWrap = (
        wrap: LogicUtil.WrapElement, ...typeList: LogicUtil.NodeType[]
    ): LogicUtil.WrapElement | null => {

        let cur: LogicUtil.WrapElement | null = wrap;
        typeList.some(type => {
            if (cur == null) return 1;
            const listName = getListParamName(cur.type);
            if (listName == null) cur = null;
            else {
                const list = cur.data[`${listName}`] as LogicUtil.WrapElement[];
                const target = list.find(child => child.type === type);
                if (target == undefined) cur = null;
                else cur = target;
            }
        });
        return cur;
    }

    export const getInnerWrapFixed = (
        wrap: LogicUtil.WrapElement, ...typeList: LogicUtil.NodeType[]
    ): LogicUtil.WrapElement => {
        const innerWrap = getInnerWrap(wrap, ...typeList);
        if (innerWrap == null) throw new Error('[innerWrap] is null.');
        return innerWrap as LogicUtil.WrapElement;
    }

    export const addChild = (wrapElement: LogicUtil.WrapElement, node: TreeUtil.ElementNode) => {
        return insertChild(wrapElement, node, node.children.length);
    }

    export const insertChild = (wrapElement: LogicUtil.WrapElement, node: TreeUtil.ElementNode, listPos: number) => {
        const wrap = node.data as LogicUtil.WrapElement;
        const listName = getListParamName(wrap.type);
        wrap.data[`${listName}`].splice(listPos, 0, wrapElement);
        const newNode = TreeUtil.createNode(wrapElement, node);
        node.children.splice(listPos, 0, newNode);
        return newNode;
    }

    export const removeChild = (node: TreeUtil.ElementNode, listPos: number, size: number) => {
        const wrap = node.data as LogicUtil.WrapElement;
        const listName = getListParamName(wrap.type);
        wrap.data[`${listName}`].splice(listPos, size);
        node.children.splice(listPos, size);
    }

    export const removeChildAll = (node: TreeUtil.ElementNode) => {
        const wrap = node.data as LogicUtil.WrapElement;
        const listName = getListParamName(wrap.type);
        wrap.data[`${listName}`].length = 0;
        node.children.length = 0;
    }

    export const isHead = (node: TreeUtil.ElementNode) => {
        if (node.parent == null) return false;
        const ownerChildren = node.parent.children;
        return ownerChildren[0] == node;
    }

    export const isTail = (node: TreeUtil.ElementNode) => {
        if (node.parent == null) return false;
        const ownerChildren = node.parent.children;
        return ownerChildren[ownerChildren.length - 1] == node;
    }

    export const getCurrentIndex = (node: TreeUtil.ElementNode) => {
        const parentWrap = node.parent?.data as LogicUtil.WrapElement;
        const listName = getListParamName(parentWrap.type);
        if (listName == null || parentWrap.data == null) return -1;
        const list = parentWrap.data[listName] as LogicUtil.WrapElement[];
        const curIndex = list.findIndex(wrap => wrap == node.data);
        return curIndex;
    }

    const swapOrder = (node: TreeUtil.ElementNode, offset: number) => {
        if (node.parent == null) return;
        const ownerChildren = node.parent.children;
        const wrap = node.parent.data as LogicUtil.WrapElement;
        const listName = getListParamName(wrap.type);
        const ownDataChildren = wrap.data[`${listName}`] as LogicUtil.WrapElement[];

        let curNo = getCurrentIndex(node);
        if (curNo === -1) return;

        // 
        const swapNode = ownerChildren[curNo];
        ownerChildren[curNo] = ownerChildren[curNo + offset];
        ownerChildren[curNo + offset] = swapNode;
        // 
        const swapData = ownDataChildren[curNo];
        ownDataChildren[curNo] = ownDataChildren[curNo + offset];
        ownDataChildren[curNo + offset] = swapData;
    }

    export const getSwapButtons = (
        node: TreeUtil.ElementNode,
        invalidate: () => void,
        ...dependencyMngNodes: TreeUtil.ElementNode[]
    ) => {
        return getSwapButtonsCallback(node, invalidate, (curIndex, isUpper) => {
            if (dependencyMngNodes.length > 0) {
                dependencyMngNodes.forEach(depNode => {
                    swapOrder(depNode.children[curIndex], isUpper ? -1 : 1);
                });
            }
        });
    }

    export const getSwapButtonsCallback = (
        node: TreeUtil.ElementNode,
        invalidate: () => void,
        callback: (curIndex: number, isUpper: boolean) => void
    ) => {
        const curIndex = getCurrentIndex(node);
        return <>
            <FormUtil.ButtonRecord
                align="center"
                buttons={[
                    {
                        label: 'Swap ',
                        isEnable: !LogicElementUtil.isHead(node),
                        callback: () => {
                            swapOrder(node, -1);
                            callback(curIndex, true);
                            invalidate();
                        }
                    }
                ]}
            />
            <FormUtil.ButtonRecord
                align="center"
                buttons={[
                    {
                        label: 'Swap ',
                        isEnable: !LogicElementUtil.isTail(node),
                        callback: () => {
                            swapOrder(node, 1);
                            callback(curIndex, false);
                            invalidate();
                        }
                    }
                ]}
            />
        </>;
    }

    /**
     * 
     * @param node 
     * @param newName 
     */
    export const replicateSameLevel = (node: TreeUtil.ElementNode, newName: string) => {
        if (node.parent == null) return;
        const wrap = node.data as LogicUtil.WrapElement;
        const cloneBase = wrap.data;
        const cloneDest = JSON.parse(JSON.stringify((cloneBase)));

        const newWrap: LogicUtil.WrapElement = {
            type: wrap.type,
            data: cloneDest
        };
        const namable = getNamableProps(wrap.type);
        newWrap.data[namable] = newName;

        // 
        const newNode = LogicElementUtil.addChild(newWrap, node.parent);
        // 
        newNode.children = LogicUtil.getChildrenElementNode(newNode);
    }

    export const getReplicateButton = (
        node: TreeUtil.ElementNode,
        isEnable: boolean,
        targetElement: string, store: Store,
        setStore: (store: Store) => void,
        invalidate: () => void
    ): JSX.Element => {
        const wrap = node.data as LogicUtil.WrapElement;
        return <FormUtil.ButtonRecord
            align="center"
            buttons={[{
                label: 'Replicate',
                isEnable,
                // isEnable: false,
                callback: () => {
                    store.system.dialog = <ConfirmDialog.Component
                        message={`Replicate this ${targetElement}.`}
                        buttons={[
                            {
                                label: 'Cancel',
                                callback: () => {
                                    store.system.dialog = null;
                                    setStore({ ...store });
                                }
                            },
                            {
                                label: 'Enter',
                                callback: () => {
                                    const namable = LogicElementUtil.getNamableProps(wrap.type);
                                    const name = wrap.data[namable] as string;
                                    const newName = LogicElementUtil.getNumberingOnPrefixName(`${name}_`, node.parent as TreeUtil.ElementNode);
                                    LogicElementUtil.replicateSameLevel(node, newName);
                                    store.system.dialog = null;
                                    setStore({ ...store });
                                    // mappingForm();
                                    invalidate();
                                }
                            }
                        ]}
                    />;
                    setStore({ ...store });
                }
            }]}
        />;
    }

    export const getListParamName = (type: LogicUtil.NodeType) => {
        switch (type) {
            case 'seed-mng':
            case 'logic-mng': return 'defs';
            case 'seed-item':
            case 'connector':
            case 'inherit-logic':
            case 'interface-mng':
            // case 'root':
            case 'logic-item': return 'mngs';
            case 'send-mng':
            case 'behavior-mng':
            case 'visual-mng':
            case 'extend-mng':
            case 'seedsend-mng':
            case 'visual-mng': return 'params';
            case 'inherit-mng': return 'inherits';
            case 'seedarg-mng':
            case 'argument-mng': return 'args';
            case 'sample-mng': return 'samples';
            case 'inhfld-mng':
            case 'field-mng': return 'fields';
            case 'thumbnail-mng': return 'items';
            case 'formgrp-mng': return 'defines';
        }
        return null;
    }

    export const getIdentifyParamName = (type: LogicUtil.NodeType) => {
        switch (type) {
            case 'argument-item':
            case 'field-item':
            case 'seed-item':
            case 'thumbnail-item':
            case 'seedarg-item':
            case 'sample-item':
            case 'formgrp-item':
            case 'logic-item': return 'name';
            case 'connector': return 'id';
        }
        return null;
    }

    export const getNamableProps = (type: LogicUtil.NodeType) => {
        switch (type) {
            case 'connector': return 'id';
            case 'seed-item':
            case 'argument-item':
            case 'seedarg-item':
            case 'field-item':
            case 'sample-item':
            case 'thumbnail-item':
            case 'logic-item': return 'name';
        }
        throw new Error('Namable props not found.');
    }

    export const syncTreeElementFromWrap = (node: TreeUtil.ElementNode) => {
        const wrap = node.data as LogicUtil.WrapElement;
        const listName = getListParamName(wrap.type);
        if (listName == null) return;
        (wrap.data[listName] as any[]).forEach(childWrap => {
            const newNode = TreeUtil.createNode(childWrap, node);
            node.children.push(newNode);
            syncTreeElementFromWrap(newNode);
        });
    }

    export const createParamWrap = (type: LogicUtil.NodeType, paramDataList: LogicUtil.NodeParamItem): LogicUtil.WrapElement => {
        return { type: type, data: paramDataList };
    }
    export const createParamWrapList = (type: LogicUtil.NodeType, paramDataList: LogicUtil.NodeParamItem[]): LogicUtil.WrapElement[] => {
        return paramDataList.map(data => {
            return { type: type, data: data };
        });
    }

    export const createVisualParams = (): LogicUtil.WrapElement[] => {
        const type = ParamUtil.VisualParamType;
        return createParamWrapList('param-item', [
            { name: type.RADIUS, formulaItems: ['var'], values: [{ type: 'direct', value: '20' }] },
            { name: type.COLOR_R, formulaItems: ['var'], values: [{ type: 'direct', value: '255' }] },
            { name: type.COLOR_G, formulaItems: ['var'], values: [{ type: 'direct', value: '255' }] },
            { name: type.COLOR_B, formulaItems: ['var'], values: [{ type: 'direct', value: '255' }] },
            { name: type.COLOR_A, formulaItems: ['var'], values: [{ type: 'direct', value: '255' }] },
        ]);
    }

    export const createBehaviorParams = (): LogicUtil.WrapElement[] => {
        const list: LogicUtil.NodeParamItem[] = [];
        const type = ParamUtil.BehaviorParamType;
        list.push({
            name: type.LIMIT,
            formulaItems: ['var'], values: [{ type: 'direct', value: '30' }]
        });
        list.push({
            name: type.POS_X,
            formulaItems: ['var'], values: [{ type: 'direct', value: '0' }]
        });
        list.push({
            name: type.POS_Y,
            formulaItems: ['var'], values: [{ type: 'direct', value: '0' }]
        });
        list.push({
            name: type.VEC_X,
            formulaItems: ['var'], values: [{ type: 'direct', value: '0' }]
        });
        list.push({
            name: type.VEC_Y,
            formulaItems: ['var'], values: [{ type: 'direct', value: '0' }]
        });
        return createParamWrapList('param-item', list);
    }

    export const getDefaultLogicMngs = (): LogicUtil.WrapElement[] => {
        return [
            {
                type: 'visual-mng',
                data: {
                    params: createVisualParams()
                } as LogicUtil.NodeParamManager
            },
            {
                type: 'behavior-mng',
                data: {
                    params: createBehaviorParams()
                } as LogicUtil.NodeParamManager
            }
        ];
    }

    export const getInitialData = (): LogicUtil.WrapElement => {
        return {
            type: 'root',
            data: {
                seedMng: {
                    type: 'seed-mng',
                    data: {
                        defs: []
                    } as LogicUtil.NodeSeedManager
                } as LogicUtil.WrapElement,
                logicMng: {
                    type: 'logic-mng',
                    data: {
                        defs: []
                    } as LogicUtil.NodeLogicManager
                } as LogicUtil.WrapElement
            } as LogicUtil.NodeRoot
        }
    }

    /**
     * 
     * @param node 
     * @returns 
     */
    export const getExistNameList = (node: TreeUtil.ElementNode) => {
        const nameList: string[] = [];
        const curWrap = node.data as LogicUtil.WrapElement;
        const nameParam = getIdentifyParamName(curWrap.type);
        const mngWrap = node.parent?.data as LogicUtil.WrapElement;
        const listParam = getListParamName(mngWrap.type);
        if (listParam == null || nameParam == null) throw Error('List param or identifiy param not found.');
        const list = mngWrap.data[listParam] as LogicUtil.WrapElement[];
        list.forEach(wrap => {
            if (wrap.data != null && curWrap != wrap) {
                nameList.push(wrap.data[nameParam]);
            }
        });
        return nameList;
    }

    export const getLogicNameList = (mng: LogicUtil.NodeLogicManager) => {
        const nameList: string[] = [];
        mng.defs.forEach(logic => {
            if (logic.data != null) {
                nameList.push((logic.data as LogicUtil.NodeLogicItem).name);
            }
        });
        return nameList;
    }

    export const getParamNameList = (curLogicOwner: LogicUtil.NodeLogicItem) => {
        const nameList: string[] = [];

        // 
        ParamUtil.VisualParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.BehaviorParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.SelfParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.ExtendParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.EffectParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.ModulateParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.Constants.forEach(name => {
            nameList.push(name);
        });

        // 
        const argMng = curLogicOwner.mngs.find(mng => mng.type === 'argument-mng');
        if (argMng != undefined) {
            const args = (argMng.data as LogicUtil.NodeArgumentManager).args;
            args.forEach(arg => {
                if (arg.data != null) {
                    nameList.push((arg.data as LogicUtil.NodeArgumentItem).name);
                }
            });
        }
        // 
        const fieldMng = curLogicOwner.mngs.find(mng => mng.type === 'field-mng');
        if (fieldMng != undefined) {
            const fields = (fieldMng.data as LogicUtil.NodeFieldManager).fields;
            fields.forEach(field => {
                if (field.data != null) {
                    nameList.push((field.data as LogicUtil.NodeParamItem).name);
                }
            });
        }
        return nameList;
    }

    /**
     * 
     * @param prefix 
     * @param node 
     */
    export const getNumberingOnPrefixName = (prefix: string, node: TreeUtil.ElementNode) => {
        let num = 0;
        // 
        while (true) {
            const check = node.children.find(child => {
                const wrap = child.data as LogicUtil.WrapElement;
                if (wrap.data == null) return false;
                console.log(wrap);
                return wrap.data[getNamableProps(wrap.type)] === `${prefix}${num}`;
            });
            if (check != undefined) num++;
            else break;
        }
        return `${prefix}${num}`;
    }

    /**
     * 
     * callback
     * @param node 
     * @param callback 
     */
    export const searchVariableReferInFormula = (node: TreeUtil.ElementNode, callback: Function) => {

        const mngType = (node.parent?.data as LogicUtil.WrapElement).type;
        const getLogicItem = () => {
            switch (mngType) {
                case 'field-mng':
                case 'argument-mng': return node.parent?.parent?.data as LogicUtil.WrapElement;
                case 'inhfld-mng': return node.parent?.parent?.parent?.parent?.data as LogicUtil.WrapElement;
            }
        }
        const logicItem = getLogicItem()?.data as LogicUtil.NodeLogicItem;
        if (mngType === 'argument-mng') {
            const fieldMng = logicItem.mngs.find(mng => mng.type === 'field-mng');
            if (fieldMng != undefined) {
                callback((fieldMng.data as LogicUtil.NodeFieldManager).fields);
            }
        }
        if (mngType !== 'inhfld-mng') {
            const visualMng = logicItem.mngs.find(mng => mng.type === 'visual-mng');
            if (visualMng != undefined) {
                callback((visualMng.data as LogicUtil.NodeParamManager).params);
            }
            const behaviorMng = logicItem.mngs.find(mng => mng.type === 'behavior-mng');
            if (behaviorMng != undefined) {
                callback((behaviorMng.data as LogicUtil.NodeParamManager).params);
            }
        }
        const inhMng = logicItem.mngs.find(mng => mng.type === 'inherit-mng');
        if (inhMng != undefined) {
            (inhMng.data as LogicUtil.NodeConnectManager).inherits.forEach(inh => {
                if (mngType !== 'inhfld-mng') {
                    const inhfld = LogicElementUtil.getInnerWrap(inh, 'inhfld-mng');
                    // InheritField
                    if (inhfld != null) {
                        callback((inhfld.data as LogicUtil.NodeFieldManager).fields);
                    }
                }
                // Extend
                callback((LogicElementUtil.getInnerWrap(inh, 'extend-mng')?.data as LogicUtil.NodeParamManager).params);
                // Send
                callback((LogicElementUtil.getInnerWrap(inh, 'send-mng')?.data as LogicUtil.NodeParamManager).params);
            });
        }
    }

    export const findSeedWrap = (rootData: LogicUtil.WrapElement, seedId: string) => {
        const root = rootData.data as LogicUtil.NodeRoot;
        const seed = (root.seedMng.data as LogicUtil.NodeSeedManager).defs.find(def => {
            const seedItem = def.data as LogicUtil.NodeSeedItem;
            return seedItem.name === seedId;
        });
        if (seed == undefined) throw new Error();
        return seed;
    }

    export const doParticleJob = (rootData: LogicUtil.WrapElement, def: LaunchDef, owner: Particle.Instance[]) => {
        const seedWrap = findSeedWrap(rootData, def.seedId);
        const seed = seedWrap.data as LogicUtil.NodeSeedItem;

        const getOrigin = () => {
            const root = rootData.data as LogicUtil.NodeRoot;
            const logicMng = (root.logicMng as LogicUtil.WrapElement).data as LogicUtil.NodeLogicManager;
            return logicMng.defs.find(wrap => (wrap.data as LogicUtil.NodeLogicItem).name === seed.entry)?.data as LogicUtil.NodeLogicItem;
        }

        const getSendingValueMap = () => {
            const seedargMng = LogicElementUtil.getInnerWrapFixed(seedWrap, 'interface-mng', 'seedarg-mng').data as LogicUtil.NodeSeedArgManager;
            const valueMap = seedargMng.args.map((wrap, i) => {
                const seedarg = wrap.data as LogicUtil.NodeSeedArgItem;
                return { name: seedarg.name, value: def.values[i] }
            });

            // SeedValueMapSendOrigin
            const sendMng = LogicElementUtil.getInnerWrapFixed(seedWrap, 'interface-mng', 'seedsend-mng').data as LogicUtil.NodeSeedSendManager;
            const sendParams = sendMng.params.map(send => send.data as LogicUtil.NodeParamItem);
            return sendParams.map(item => {
                const value = PreviewUtil.convertParamsToValue(item.name, sendParams, valueMap);
                if (value == undefined) throw new Error(`[${item.name}]Send`);
                return { name: item.name, value }
            });
        }

        owner.push(new Particle.Instance(
            PreviewUtil.buildLogicProps(owner, getOrigin(), rootData, getSendingValueMap())
        ));
    }

    export type SeedProps = {
        seed: LogicUtil.NodeSeedItem;
        logic: LogicUtil.NodeLogicItem;
        seedargList: LogicUtil.NodeSeedArgItem[];
        seedsendList: LogicUtil.NodeParamItem[];
        thumbnailList: LogicUtil.NodeThumbnailItem[];
    }
    export const getSeedProperties = (rootData: LogicUtil.WrapElement, seedId: string) => {
        const seedWrap = findSeedWrap(rootData, seedId);
        const seed = seedWrap.data as LogicUtil.NodeSeedItem;

        const getOrigin = () => {
            const root = rootData.data as LogicUtil.NodeRoot;
            const logicMng = (root.logicMng as LogicUtil.WrapElement).data as LogicUtil.NodeLogicManager;
            return logicMng.defs.find(wrap => (wrap.data as LogicUtil.NodeLogicItem).name === seed.entry)?.data as LogicUtil.NodeLogicItem;
        }
        const seedargMng = LogicElementUtil.getInnerWrapFixed(seedWrap, 'interface-mng', 'seedarg-mng').data as LogicUtil.NodeSeedArgManager;
        const seedargList = seedargMng.args.map(wrap => wrap.data as LogicUtil.NodeSeedArgItem);
        const seedsendMng = LogicElementUtil.getInnerWrapFixed(seedWrap, 'interface-mng', 'seedsend-mng').data as LogicUtil.NodeSeedSendManager;
        const seedsendList = seedsendMng.params.map(wrap => wrap.data as LogicUtil.NodeParamItem);
        const thumbnailMng = LogicElementUtil.getInnerWrapFixed(seedWrap, 'thumbnail-mng').data as LogicUtil.NodeThumnailManager;
        const thumbnailList = thumbnailMng.items.map(wrap => wrap.data as LogicUtil.NodeThumbnailItem);
       
        return {
            seed, logic: getOrigin(), seedargList, seedsendList, thumbnailList
        }
    }
}

export default LogicElementUtil;