namespace VectorUtil {

    export type XY = {
        x: number, y: number;
    }

    export class Vector {

        xy: XY;
        rad: number;
        len: number;

        constructor(xy: XY) {
            this.xy = xy;
            this.rad = 0;
            this.len = 0;
            this.syncFromXY();
        }

        syncFromXY = () => {
            this.len = Math.sqrt(this.xy.x * this.xy.x + this.xy.y * this.xy.y);
            this.rad = Math.atan2(this.xy.y, this.xy.x);
        }

        syncFromRad = () => {
            this.xy.x = Math.cos(this.rad) * this.len;
            this.xy.y = Math.sin(this.rad) * this.len;
        }

    }
}

export default VectorUtil;