import { useContext } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import TreeUtil from "../../../../../utils/treeUtil";
import LogicMainteTab, { FactoryTreeContext } from "../../logicMainteTab";
import LogicUtil from "../../util/logicUtil";

const EditorSeedMng = () => {
    const {node, setFocusNode} = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    return (<>
        <FormUtil.BorderPanel
            title="operation"
            innerJsx={<FormUtil.ButtonRecord
                align="center"
                buttons={[
                    {
                        label: 'Add Seed',
                        callback: () => {
                            const itemWrap: LogicUtil.WrapElement = {
                                type: 'seed-item',
                                data: null
                            };
                            (wrap.data as LogicUtil.NodeSeedManager).defs.push(itemWrap);
                            const newNode = TreeUtil.createNode(itemWrap, node);
                            node.children.push(newNode);
                            setFocusNode(newNode);
                        }
                    }
                ]}
            />}
        />
    </>);
}

export default EditorSeedMng;