import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../../utils/form/validateUtil";
import TreeUtil from "../../../../../utils/treeUtil";
import { GlobalContext } from "../../../../entry/systemEntry";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicUtil from "../../util/logicUtil";
import ModifyOperations from "../common/modifyOperations";
import LogicTemplateDialog from "../../dialog/logicTemplateDialog";
import LogicElementUtil from "../../util/logicElementUtil";
import ConfirmDialog from "../../../../dialog/confirmDialog";

const EditorLogicItem = () => {
    const { store, setStore } = useContext(GlobalContext);
    const { removeSelf, node, setFocusNode, invalidate, setModify, isModify } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });

    const [isConnect, hasArgNode, hasFieldNode, hasConnectNode] = useMemo(() => {
        const list = node.children.map(el => {
            return (el.data as LogicUtil.WrapElement).type;
        });
        const parentType = (node.parent?.data as LogicUtil.WrapElement).type;
        return [
            parentType === 'connector',
            list.includes('argument-mng'),
            list.includes('field-mng'),
            list.includes('inherit-mng'),
        ];
    }, [node]);

    const mappingForm = () => {
        if (!isConnect) {
            const param = wrap.data as LogicUtil.NodeLogicItem;
            setName({ ...name, value: param.name });
        }
    };

    /**  */
    const existNameList = useMemo(() => {
        // 
        if (wrap.type === 'inherit-logic') return [];
        return LogicElementUtil.getExistNameList(node);
    }, [node]);

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node, isConnect]);

    /**
     * 
     */
    const toggleElement = (hasNode: boolean, listPos: number, wrapElement: LogicUtil.WrapElement) => {
        if (hasNode) {
            LogicElementUtil.removeChild(node, listPos, 1);
            setFocusNode({ ...node });
            invalidate();
        } else {
            LogicElementUtil.insertChild(wrapElement, node, listPos);
            setFocusNode({ ...node });
            invalidate();
        }
    };

    /**
     * 
     */
    const hasArgument = () => {
        const argumentNode = LogicElementUtil.getInnerNode(node, 'argument-mng');
        if (argumentNode == null) return false;
        return argumentNode.children.length > 0;
    }
    /**
     * 
     */
    const hasField = () => {
        const fieldNode = LogicElementUtil.getInnerNode(node, 'field-mng');
        if (fieldNode == null) return false;
        return fieldNode.children.length > 0;
    }
    /**
     * 
     */
    const hasInherit = () => {
        const inheritNode = LogicElementUtil.getInnerNode(node, 'inherit-mng');
        if (inheritNode == null) return false;
        return inheritNode.children.length > 0;
    }

    return (<>
        {isConnect ? <></> : (<>
            <ModifyOperations.Component
                mappingForm={mappingForm}
                checkableFields={[name]}
                firstRegist={() => {
                    const element: LogicUtil.NodeLogicItem = {
                        name: name.value,
                        mngs: LogicElementUtil.getDefaultLogicMngs()
                    };
                    element.name = name.value;
                    wrap.data = element;

                    element.mngs.forEach(mng => {
                        const newNode = TreeUtil.createNode(mng, node);
                        const data = (newNode.data as LogicUtil.WrapElement).data;
                        (data as LogicUtil.NodeParamManager).params.forEach(param => {
                            newNode.children.push(TreeUtil.createNode(param, newNode));
                        });
                        node.children.push(newNode);
                    });
                    invalidate();
                    setStore({ ...store });
                }}
                modifyRegist={() => {
                    const param = wrap.data as LogicUtil.NodeLogicItem;
                    const prevName = param.name;
                    param.name = name.value;
                    wrap.data = param;
                    // 
                    if (prevName !== param.name) {
                        syncLogicName(node, prevName, param.name);
                    }
                }}
            />
            <FormUtil.BorderPanel
                title="param"
                innerJsx={<>
                    <FormUtil.FormRecord
                        titleLabel="Name"
                        jsx={
                            <FormUtil.TextField
                                width={200}
                                isReadOnly={!isModify}
                                checkable={name}
                                setCheckable={setName}
                                validates={[
                                    {
                                        checker: (value) => ValidateUtil.checkRequired(value),
                                        errorType: "required"
                                    },
                                    {
                                        checker: (value) => ValidateUtil.checkStringLength(value, 1, 16),
                                        errorType: "length"
                                    },
                                    {
                                        checker: (value) => LogicUtil.checkFieldName(value),
                                        errorType: "value"
                                    },
                                    {
                                        checker: (value) => !existNameList.includes(value),
                                        errorType: "relation"
                                    }
                                ]}
                            />
                        }
                    />
                </>}
            />
        </>)}
        <FormUtil.BorderPanel
            title="element"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={<FormUtil.Switch
                        label={'Argument'}
                        width={300}
                        isUse={hasArgNode}
                        isEnable={!hasArgument()}
                        callback={() => {
                            let listPos = 0;
                            toggleElement(
                                hasArgNode,
                                listPos,
                                {
                                    type: 'argument-mng',
                                    data: {
                                        args: []
                                    } as LogicUtil.NodeArgumentManager
                                }
                            );
                        }}
                    />}
                />
                <FormUtil.Record
                    textAlign="center"
                    isEnable={true}
                    jsx={<FormUtil.Switch
                        label={'Field'}
                        width={300}
                        isUse={hasFieldNode}
                        isEnable={!hasField()}
                        callback={() => {
                            let listPos = 0;
                            if (hasArgNode) listPos++;
                            toggleElement(
                                hasFieldNode,
                                listPos,
                                {
                                    type: 'field-mng',
                                    data: {
                                        fields: []
                                    } as LogicUtil.NodeFieldManager
                                }
                            );
                        }}
                    />}
                />
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Inherit'}
                            width={300}
                            isUse={hasConnectNode}
                            isEnable={!hasInherit()}
                            callback={() => {
                                let listPos = 2;
                                if (hasArgNode) listPos++;
                                if (hasFieldNode) listPos++;
                                toggleElement(
                                    hasConnectNode,
                                    listPos,
                                    {
                                        type: 'inherit-mng',
                                        data: {
                                            inherits: []
                                        } as LogicUtil.NodeConnectManager
                                    }
                                );
                            }}
                        />
                    }
                />
            </>}
        />
        {isConnect ? <></> : (
            <FormUtil.BorderPanel
                title="operation"
                isEnable={!isModify}
                innerJsx={<>
                    {LogicElementUtil.getReplicateButton(node, !isModify, 'logic', store, setStore, invalidate)}
                    {/* <FormUtil.ButtonRecord
                    align="center"
                        buttons={[
                            {
                                label: 'Replicate',
                                isEnable: !isModify,
                                // isEnable: false,
                                callback: () => {
                                    store.system.dialog = <ConfirmDialog.Component
                                        message="Replicate this logic."
                                        buttons={[
                                            {
                                                label: 'Cancel',
                                                callback: () => {
                                                    store.system.dialog = null;
                                                    setStore({ ...store });
                                                }
                                            },
                                            {
                                                label: 'Enter',
                                                callback: () => {
                                                    const namable = LogicElementUtil.getNamableProps(wrap.type);
                                                    const name = wrap.data[namable] as string;
                                                    const newName = LogicElementUtil.getNumberingOnPrefixName(`${name}_`, node.parent as TreeUtil.ElementNode);
                                                    LogicElementUtil.replicateSameLevel(node, newName);
                                                    store.system.dialog = null;
                                                    setStore({ ...store });
                                                    // mappingForm();
                                                    invalidate();
                                                }
                                            }
                                        ]}
                                    />;
                                    setStore({ ...store });
                                }
                            }
                        ]}
                    /> */}
                    <FormUtil.ButtonRecord
                        align="center"
                        buttons={[
                            {
                                label: 'Delete',
                                callback: () => {
                                    const param = wrap.data as LogicUtil.NodeArgumentItem;
                                    const refers = getLocgicRefers(node, param.name);
                                    if (refers.length === 0) {
                                        removeSelf();
                                    } else {
                                        store.system.dialog = <ConfirmDialog.Component
                                            message={`[${param.name}] is referenced in the Inherit.\n${refers}`}
                                        />;
                                        setStore({ ...store });
                                    }
                                }
                            }
                        ]}
                    />
                    {/*  */}
                    {LogicElementUtil.getSwapButtons(node, invalidate)}
                </>}
            />
        )}
    </>);
}

export default EditorLogicItem;

/**
 * Inherit
 * @param node 
 * @param logicName 
 * @returns Inherit
 */
export const getLocgicRefers = (node: TreeUtil.ElementNode, logicName: string) => {

    const refers: string[] = [];
    const callback = (conn: LogicUtil.NodeInheritConnector) => {
        refers.push(conn.id);
    }
    searchLogicReferInherit(node, logicName, callback);
    return refers;
}

/**
 * 
 * @param node 
 */
const syncLogicName = (node: TreeUtil.ElementNode, prevName: string, newName: string) => {
    const callback = (conn: LogicUtil.NodeInheritConnector) => {
        conn.depend = newName;
    }
    searchLogicReferInherit(node, prevName, callback);
}

const searchLogicReferInherit = (
    node: TreeUtil.ElementNode,
    logicName: string,
    callback: (conn: LogicUtil.NodeInheritConnector) => void
) => {
    const logicMngNode = node.parent as TreeUtil.ElementNode
    const runRec = (logic: LogicUtil.NodeLogicItem) => {
        const inhMng = logic.mngs.find(mng => mng.type === 'inherit-mng');
        if (inhMng == undefined) return;
        (inhMng.data as LogicUtil.NodeConnectManager).inherits.forEach(inh => {
            const conn = inh.data as LogicUtil.NodeInheritConnector;
            if (conn.method === 'create') {
                const inhLogic = LogicElementUtil.getInnerWrap(inh, 'inherit-logic');
                runRec(inhLogic?.data);
            } else {
                if (conn.depend === logicName) {
                    callback(conn);
                }
            }
        });
    }
    logicMngNode.children.forEach(logic => {
        runRec((logic.data as LogicUtil.WrapElement).data);
    });
}