import VectorUtil from "./vectorUtil";

namespace Particle {

    export type Props = {
        owner: Instance[];
        pos: VectorUtil.XY;
        vec: VectorUtil.Vector;
        limit: number;
        drawer: (self: Instance, ctx: CanvasRenderingContext2D) => void;
        extend: ((self: Instance) => void)[];
    }

    export class Instance {

        owner: Instance[];
        pos: VectorUtil.XY;
        prevPos: VectorUtil.XY;
        vec: VectorUtil.Vector;
        frame: number;
        limit: number;
        isLive: boolean;
        drawer: (self: Instance, ctx: CanvasRenderingContext2D) => void;
        extend: ((self: Instance) => void)[];

        constructor(props: Props) {
            this.owner = props.owner;
            this.pos = props.pos;
            this.prevPos = props.pos;
            this.vec = props.vec;

            this.frame = 0;
            this.limit = props.limit;
            this.isLive = true;
            this.drawer = props.drawer;
            this.extend = props.extend;
        }

        update = () => {

            this.pos.x += this.vec.xy.x;
            this.pos.y += this.vec.xy.y;

            this.extend.forEach(callback => callback(this));

            if (this.frame === this.limit) {
                this.isLive = false;
            }
            this.frame++;
        }

        draw = (ctx: CanvasRenderingContext2D) => {
            this.drawer(this, ctx);
        }
    }
}

export default Particle;