import { useContext, useEffect } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicUtil from "../../util/logicUtil";

namespace ModifyOperations {

    export type Props = {
        mappingForm: () => void;
        checkableFields: FormUtil.CheckableValue[];
        isInputOK?: boolean;
        firstRegist: () => void;
        modifyRegist: () => void;
    }

    export const Component = (props: Props) => {
        const { node, isModify, setModify, removeSelf } = useContext(FactoryTreeContext);

        const wrap = node.data as LogicUtil.WrapElement;

        const isComplete = FormUtil.isCompleteCheck(props.checkableFields);

        return (<>
            <FormUtil.ButtonRecord
                align="center"
                buttons={[
                    {
                        label: 'Cancel',
                        isEnable: isModify,
                        callback: () => {
                            if (wrap.data == null) {
                                removeSelf();
                            } else {
                                props.mappingForm();
                            }
                            setModify(false);
                        }
                    },
                    {
                        label: !isModify ? 'Modify' : 'Regist',
                        isEnable: !isModify || (props.isInputOK ?? isComplete),
                        callback: () => {
                            if (!isModify) {
                                setModify(true);
                            } else {
                                if (wrap.data == null) {
                                    props.firstRegist();
                                } else {
                                    props.modifyRegist();
                                }
                                setModify(false);
                            }
                        }
                    }
                ]}
            />
        </>);
    }
}

export default ModifyOperations;