import styled from "styled-components";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import { LaunchDef, SeedBean } from "../launcherTab";
import { useContext, useEffect, useMemo, useState } from "react";
import LogicFormUtil from "../../factory/util/logicFormUtil";
import LogicUtil from "../../factory/util/logicUtil";
import { GlobalContext } from "../../../entry/systemEntry";


const LaunchProperty = (props: {
    newFrame: FormUtil.CheckableValue;
    setNewFrame: (newFrame: FormUtil.CheckableValue) => void;
    defBean: LaunchDef;
    seedList: SeedBean[];
    isModify: boolean;
    cancelAction: () => void;
    modifyAction: (frame: number, seedId: string, values: number[]) => void;
}) => {
    const { store, setStore } = useContext(GlobalContext);

    const [seed, setSeed] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [template, setTemplate] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [formList, setFormList] = useState<FormUtil.CheckableValue[]>([]);

    const newFrame = props.newFrame;
    const setNewFrame = props.setNewFrame;
    const isInputOK = seed.errors.length === 0 && newFrame.errors.length === 0 && formList.find(form => form.errors.length > 0) == undefined;


    const mapping = () => {
        setNewFrame({ ...newFrame, value: props.defBean.frame.toString() });
        setSeed({ ...seed, value: props.defBean.seedId.toString() });
        setFormList(props.defBean.values.map(value => {
            return { value: value.toString(), errors: [] };
        }));
    }

    useEffect(() => {
        if (props.defBean == undefined) return;
        if (props.defBean.seedId === '') {
            setNewFrame({ ...newFrame, value: '' });
            setSeed({ ...seed, value: '' });
            setFormList([]);
        } else {
            mapping();
        }
    }, [props.defBean]);

    const initFormList = (seedId: string) => {
        let argumentList: LogicUtil.NodeSeedArgItem[] = [];

        const activeSeed = props.seedList.find(item => {
            return item.name === seedId;
        });
        if (activeSeed != undefined) {
            argumentList = activeSeed.args;
        }
        setFormList(argumentList.map(arg => {
            let value = '';
            if (arg.defval != undefined) value = arg.defval.toString();
            return { value, errors: [] };
        }));
    }

    const propertiesFormJsx = useMemo(() => {
        if (seed.value === '') return <></>;

        let argumentList: LogicUtil.NodeSeedArgItem[] = [];
        let formgrpList: LogicUtil.NodeFromGrpItem[] = [];
        let presetList: LogicUtil.NodeSampleItem[] = [];

        const activeSeed = props.seedList.find(item => {
            return item.name === seed.value;
        });
        if (activeSeed != undefined) {
            argumentList = activeSeed.args;
            formgrpList = activeSeed.formgrps;
            presetList = activeSeed.presets;
        }
        return (<>
            <FormUtil.BorderPanel
                title="Preset"
                innerJsx={<FormUtil.Record
                    jsx={<>
                        <FormUtil.Combobox
                            width={250}
                            checkable={template}
                            setCheckable={setTemplate}
                            headBlank
                            list={presetList.map((sample, i) => {
                                return {
                                    labelText: sample.name,
                                    value: i.toString()
                                }
                            })}
                            isReadOnly={!props.isModify}
                        />
                        <FormUtil.ButtonItem
                            button={{
                                label: 'Apply',
                                isEnable: template.value !== '',
                                callback: () => {
                                    const selectedIndex = Number(template.value);
                                    const values = presetList[selectedIndex].values;
                                    values.forEach((value, i) => {
                                        formList[i].value = value.toString();
                                    });
                                    setFormList(formList.slice());
                                }
                            }}
                        />
                    </>}
                />}
            />
            <FormUtil.BorderPanel
                title="Properties"
                innerJsx={<>{
                    LogicFormUtil.buildPreviewForms(
                        !props.isModify, argumentList, formgrpList, formList, setFormList
                    )
                }</>}
            />
        </>);
    }, [template, formList, props.isModify]);

    return (
        <_Wrap>
            <FormUtil.ButtonRecord
                align="center"
                buttons={[
                    {
                        label: 'Cancel',
                        isEnable: props.isModify,
                        callback: () => {
                            props.cancelAction();
                            setTemplate({ ...template, value: '' });
                            mapping();
                        }
                    },
                    {
                        label: !props.isModify ? 'Modify' : 'Regist',
                        isEnable: !props.isModify || isInputOK,
                        callback: () => {
                            props.modifyAction(
                                Number(newFrame.value),
                                seed.value,
                                formList.map(form => Number(form.value))
                            );
                            setTemplate({ ...template, value: '' });
                        }
                    }
                ]}
            />
            <FormUtil.BorderPanel
                title="Base"
                innerJsx={<>
                    <FormUtil.FormRecord
                        titleLabel="Frame"
                        jsx={
                            <FormUtil.TextField
                                width={100}
                                isReadOnly={!props.isModify}
                                isNumber
                                checkable={newFrame}
                                setCheckable={setNewFrame}
                                validates={[
                                    {
                                        checker: (value) => ValidateUtil.checkRequired(value),
                                        errorType: "required"
                                    },
                                    {
                                        checker: (value) => ValidateUtil.checkNumberRange(value, 0, store.launchData.limitFrame),
                                        errorType: "value"
                                    }
                                ]}
                            />
                        }
                    />
                    <FormUtil.FormRecord
                        titleLabel="Seed"
                        jsx={
                            <FormUtil.Combobox
                                checkable={seed}
                                setCheckable={(checkable) => {
                                    initFormList(checkable.value);
                                    setSeed({ ...checkable });
                                }}
                                list={props.seedList.map(seed => {
                                    return { value: seed.name, labelText: seed.name }
                                })}
                                headBlank
                                width={200}
                                isReadOnly={!props.isModify}
                                validates={
                                    [
                                        {
                                            checker: (value) => ValidateUtil.checkRequired(value),
                                            errorType: "required"
                                        }
                                    ]
                                }
                            />
                        }
                    />
                </>}
            />
            {propertiesFormJsx}
        </_Wrap>
    )
}

export default LaunchProperty;

const _Wrap = styled.div`
    display: inline-block;
    position: relative;
    background-color: #d1d1d1;
    width: 100%;
    height: 100%;
    overflow-y: auto;
`;

const _Head = styled.div`
    display: inline-block;
    position: relative;
    background-color: #dbebd6;
    width: 100%;
    height: 40px;
`;
