import Particle from "./particle";

namespace DrawUtil {

    export type Rgba = {
        r: number;
        g: number;
        b: number;
        a: number;
    }

    export type ColorStop = {
        offset: number;
        color: string;
    }

    export type DrawerParam = {
        color: string;
        radius?: number;
        disappearStartRate?: number;
        shrinkStartRate?: number;
        flashAppearFrame?: number;
        flashHideFrame?: number;
    }

    export const createGradDrawer = (param: DrawerParam) => {

        /**  */
        const radiusBase = param.radius ?? 30;
        return (self: Particle.Instance, ctx: CanvasRenderingContext2D) => {
            if (param.flashAppearFrame != undefined) {
                const appearFrame = param.flashAppearFrame;
                const hideFrame = (param.flashHideFrame as number);
                const unitFrame = appearFrame + hideFrame;
                if ((self.frame % unitFrame) >= appearFrame) return;
            }

            let radius = radiusBase;
            if (param.shrinkStartRate != undefined) {
                const start = Math.floor(self.limit * param.shrinkStartRate);
                if (self.frame > start) {
                    const limit = self.limit - start;
                    const frame = self.frame - start;
                    const rate = (limit - frame) / limit;
                    radius *= rate;
                }
            }

            const x = self.pos.x;
            const y = self.pos.y;
            const g = ctx.createRadialGradient(x, y, 1, x, y, radius);

            const grads: ColorStop[] = [];
            const rgba = convertRgbaFromColorCode(param.color) ?? { r: 255, g: 255, b: 255, a: 255 };
            grads.push({ offset: 0, color: convertColorCodeFromRgba({ r: 255, g: 255, b: 255, a: Math.floor(rgba.a * 0.9) }) });
            grads.push({ offset: 0.2, color: convertColorCodeFromRgba({ ...rgba, a: Math.floor(rgba.a * 0.15) }) });
            grads.push({ offset: 1, color: convertColorCodeFromRgba({ ...rgba, a: 1 }) });

            grads.forEach(grad => {
                g.addColorStop(grad.offset, grad.color);
            });

            ctx.globalAlpha = 1;
            if (param.disappearStartRate != undefined) {
                const start = Math.floor(self.limit * param.disappearStartRate);
                if (self.frame > start) {
                    const limit = self.limit - start;
                    const frame = self.frame - start;
                    ctx.globalAlpha = (limit - frame) / limit;
                }
            }

            ctx.fillStyle = g;
            // ctx.fillStyle = param.color;
            ctx.beginPath();
            ctx.arc(x, y, radius, 0, 2 * Math.PI, false);
            ctx.fill();
        };
    }

    export const convertRgbaFromColorCode = (code: string): Rgba | null => {
        const div = code.split('#');
        if (div.length !== 2 || div[1].length < 6) return null;

        const value = div[1];
        const r = Number.parseInt(value.substring(0, 2), 16);
        const g = Number.parseInt(value.substring(2, 4), 16);
        const b = Number.parseInt(value.substring(4, 6), 16);
        let a = 1;
        if (value.length === 8) {
            a = Number.parseInt(value.substring(6, 8), 16);
        }
        return { r, g, b, a };
    }

    export const convertColorCodeFromRgba = (rgba: Rgba): string => {
        const r1 = (Math.floor(rgba.r));
        const g1 = (Math.floor(rgba.g));
        const b1 = (Math.floor(rgba.b));
        const a1 = (Math.floor(rgba.a));
        const r = ('00' + r1.toString(16)).slice(-2);
        const g = ('00' + g1.toString(16)).slice(-2);
        const b = ('00' + b1.toString(16)).slice(-2);
        const a = ('00' + a1.toString(16)).slice(-2);
        return `#${r}${g}${b}${a}`;
    }

    export const toWhite = (color: string, rate: number): string => {
        return color;
    }
}

export default DrawUtil;