import { useContext, useEffect, useState } from "react";
import styled, { css } from "styled-components";
import { TabbedPane } from "../../base/components/tab/tabbedPane";
import Styles, { _CSS_LABEL_MIDIUM } from "../../base/design/styles";
import { SCORE_TAB_MARGIN } from "../../utils/systemConst";
import { GlobalContext } from "../entry/systemEntry";
import LogicMainteTab from "./factory/logicMainteTab";
import ManageTab from "./manage/manageTab";
import PreviewTab from "./manage/manageTab";
import LauncherTab from "./launcher/launcherTab";
import Timeline from "./launcher/launcherTab";


const CreateChannel = () => {

    const { store, setStore } = useContext(GlobalContext);

    const hasData = store.logicData != null;
    return (
        <_Wrap>
            <TabbedPane
                tabElements={[
                    { name: 'Manage', enable: true, cont: <ManageTab /> },
                    { name: 'Launcher', enable: hasData, cont: <LauncherTab /> },
                    { name: 'Library', enable: hasData, cont: <LogicMainteTab /> }
                ]}
                activeNo={0}
            // selectTabIndex={(index: number) => dispatcher.score.setScoreTabIndex(index)}
            />
        </_Wrap>
    );
}
export default CreateChannel;

const _Wrap = styled.div`
    display: inline-block;
    position: relative;
    margin: ${SCORE_TAB_MARGIN}px 0 0 ${SCORE_TAB_MARGIN}px;
    width: calc(100% - ${SCORE_TAB_MARGIN * 2}px);
    height: calc(100% - ${SCORE_TAB_MARGIN * 2}px);
    background-color: transparent;
    /* background-color: #c3d8dd6f; */
    /* border: solid 1px black;
    border-radius: 5px; */
    /* box-shadow: 0 2px 14px 4px #000b3a48; */
`;

