import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../../utils/form/validateUtil";
import TreeUtil from "../../../../../utils/treeUtil";
import ConfirmDialog from "../../../../dialog/confirmDialog";
import { GlobalContext } from "../../../../entry/systemEntry";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicElementUtil from "../../util/logicElementUtil";
import LogicUtil from "../../util/logicUtil";
import ModifyOperations from "../common/modifyOperations";

const EditorSeedArgItem = () => {
    const { store, setStore } = useContext(GlobalContext);
    const { node, removeSelf, setModify, isModify, invalidate } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [disp, setDisp] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [min, setMin] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [max, setMax] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [step, setStep] = useState<FormUtil.CheckableValue>({ value: '1', errors: [] });
    const [defval, setDefval] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    // const [argType, setArgType] = useState<FormUtil.CheckableValue>({ value: 'number', errors: [] });

    const seedNode = node.parent?.parent?.parent as TreeUtil.ElementNode;
    const sampleMngNode = LogicElementUtil.getInnerNodeFixed(seedNode, 'sample-mng');

    /**  */
    const reservedList = useMemo(() => {
        const owner = (node.parent?.parent?.data as LogicUtil.WrapElement).data;
        return LogicElementUtil.getParamNameList(owner);
    }, [node]);

    const mappingForm = () => {
        const param = wrap.data as LogicUtil.NodeSeedArgItem;
        setName({ ...name, value: param.name });
        setDisp({ ...name, value: param.disp ?? '' });
        setMin({ ...min, value: param.min.toString() });
        setMax({ ...max, value: param.max.toString() });
        setStep({ ...step, value: param.step.toString() });
        setDefval({ ...defval, value: param.defval == undefined ? '' : param.defval.toString() });
        // setArgType({ ...argType, value: param.valueType });
    };

    /**  */
    const existNameList = useMemo(() => LogicElementUtil.getExistNameList(node), [node]);

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node]);

    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            checkableFields={[name, disp, min, max, step, defval]}
            firstRegist={() => {
                const param: LogicUtil.NodeSeedArgItem = {
                    name: name.value,
                    disp: disp.value === '' ? undefined : disp.value,
                    min: Number(min.value),
                    max: Number(max.value),
                    step: Number(step.value),
                    defval: defval.value === '' ? undefined : Number(defval.value),
                    // valueType: argType.value as LogicUtil.ArgumentType
                };
                wrap.data = param;
                syncArgumentAdd(seedNode as TreeUtil.ElementNode, param);
            }}
            modifyRegist={() => {
                const param = wrap.data as LogicUtil.NodeSeedArgItem;
                const prevName = param.name;
                param.name = name.value;
                param.disp = disp.value === '' ? undefined : disp.value;
                param.min = Number(min.value);
                param.max = Number(max.value);
                param.step = Number(step.value);
                param.defval = defval.value === '' ? undefined : Number(defval.value);
                // param.valueType = argType.value as LogicUtil.ArgumentType;
                wrap.data = param;

                if (prevName !== name.value) {
                    syncArgumentName(seedNode, prevName, name.value);
                }
            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Name"
                    jsx={
                        <FormUtil.TextField
                            width={200}
                            isReadOnly={!isModify}
                            checkable={name}
                            setCheckable={setName}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 1, 16),
                                    errorType: "length"
                                },
                                {
                                    checker: (value) => LogicUtil.checkFieldName(value),
                                    errorType: "value"
                                },
                                {
                                    checker: (value) => !existNameList.includes(value),
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Display"
                    jsx={
                        <FormUtil.TextField
                            width={200}
                            isReadOnly={!isModify}
                            checkable={disp}
                            setCheckable={setDisp}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 0, 16),
                                    errorType: "length"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Min"
                    jsx={
                        <FormUtil.TextField
                            width={100}
                            isReadOnly={!isModify}
                            checkable={min}
                            setCheckable={setMin}
                            isNumber
                            dependency={[max]}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => Number(value) < Number(max.value),
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Max"
                    jsx={
                        <FormUtil.TextField
                            width={100}
                            isReadOnly={!isModify}
                            checkable={max}
                            setCheckable={setMax}
                            isNumber
                            dependency={[min]}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => Number(value) > Number(min.value),
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Step"
                    jsx={
                        <FormUtil.TextField
                            width={100}
                            isReadOnly={!isModify}
                            checkable={step}
                            setCheckable={setStep}
                            isNumber
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Default"
                    jsx={
                        <FormUtil.TextField
                            width={100}
                            isReadOnly={!isModify}
                            checkable={defval}
                            setCheckable={setDefval}
                            isNumber
                        />
                    }
                />
                {/* <FormUtil.FormRecord
                    titleLabel="Type"
                    jsx={
                        <FormUtil.Combobox
                            checkable={argType}
                            setCheckable={setArgType}
                            list={LogicUtil.ArgTypes.map(type => {
                                return { value: type, labelText: type, isDisabled: type === 'color' }
                            })}
                            headBlank
                            width={200}
                            isReadOnly={!isModify}
                            validates={
                                [
                                    {
                                        checker: (value) => ValidateUtil.checkRequired(value),
                                        errorType: "required"
                                    }
                                ]
                            }
                        />
                    }
                /> */}
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            isEnable={!isModify}
            innerJsx={<>
                {LogicElementUtil.getReplicateButton(node, !isModify, 'argument', store, setStore, invalidate)}
                <FormUtil.ButtonRecord
                    align="center"
                    buttons={[
                        {
                            label: 'Delete',
                            isEnable: !isModify,
                            callback: () => {
                                const param = wrap.data as LogicUtil.NodeSeedArgItem;
                                const refers = getArgumentRefers(seedNode, param.name);
                                if (refers.length === 0) {
                                    const curIndex = LogicElementUtil.getCurrentIndex(node);
                                    if (curIndex === -1) throw new Error('[curIndex] is -1.');
                                    syncArgumentDel(seedNode, curIndex);
                                    removeSelf();
                                } else {
                                    store.system.dialog = <ConfirmDialog.Component
                                        message={`[${param.name}] is referenced in the formula.\n${refers}`}
                                    />;
                                    setStore({ ...store });
                                }
                            }
                        }
                    ]}
                />
                {/*  */}
                {LogicElementUtil.getSwapButtonsCallback(node, invalidate, (index, isUpper) => {
                    const sampleMng = (sampleMngNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeSampleManager;
                    const offset = isUpper ? -1 : 1;
                    sampleMng.samples.forEach(wrap => {
                        const values = (wrap.data as LogicUtil.NodeSampleItem).values;
                        const swapNode = values[index];
                        values[index] = values[index + offset];
                        values[index + offset] = swapNode;
                    });
                })}
            </>}
        />
    </>);
}

/**
 * 
 * @param argName 
 * @returns 
 */
const getArgumentRefers = (seedNode: TreeUtil.ElementNode, argName: string) => {

    const refers: string[] = [];

    const sendMngWrap = LogicElementUtil.getInnerWrapFixed(seedNode.data as LogicUtil.WrapElement, 'interface-mng', 'seedsend-mng');
    const sendMng = sendMngWrap.data as LogicUtil.NodeSeedSendManager;
    sendMng.params.forEach(wrap => {
        const param = wrap.data as LogicUtil.NodeParamItem;
        param.values.forEach(varItem => {
            if (varItem.type === 'argument' && varItem.value === argName) {
                refers.push(param.name);
            }
        });
    });
    return refers;
}

export default EditorSeedArgItem;

/**
* 
* @param seedNode Seed
*/
const syncArgumentName = (seedNode: TreeUtil.ElementNode, prevName: string, newName: string) => {

    const syncFormula = (list: LogicUtil.WrapElement[]) => {
        list.forEach(wrap => {
            const values = (wrap.data as LogicUtil.NodeParamItem).values;
            values.forEach(varItem => {
                if (varItem.type === 'argument' && varItem.value === prevName) {
                    varItem.value = newName;
                }
            });
        });
    }
    const sendMngWrap = LogicElementUtil.getInnerWrapFixed(seedNode.data as LogicUtil.WrapElement, 'interface-mng', 'seedsend-mng');
    syncFormula((sendMngWrap.data as LogicUtil.NodeParamManager).params);

}

/**
 * Sample
 * @param seedNode Seed
 */
const syncArgumentAdd = (seedNode: TreeUtil.ElementNode, arg: LogicUtil.NodeSeedArgItem) => {
    const sampleMngNode = LogicElementUtil.getInnerNodeFixed(seedNode, 'sample-mng');
    const sampleMng = (sampleMngNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeSampleManager;
    sampleMng.samples.forEach(wrap => {
        const sample = wrap.data as LogicUtil.NodeSampleItem;
        sample.values.push(arg.defval ?? arg.min);
    });
}

/**
 * Send
 * @param seedNode Seed
 */
const syncArgumentDel = (seedNode: TreeUtil.ElementNode, delIndex: number) => {
    const sampleMngNode = LogicElementUtil.getInnerNodeFixed(seedNode, 'sample-mng');
    const sampleMng = (sampleMngNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeSampleManager;
    sampleMng.samples.forEach(wrap => {
        const sample = wrap.data as LogicUtil.NodeSampleItem;
        sample.values.splice(delIndex, 1);
    });
}