import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import TreeUtil from "../../../../../utils/treeUtil";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicElementUtil from "../../util/logicElementUtil";
import LogicUtil from "../../util/logicUtil";
import ParamUtil from "../../util/paramUtil";

const EditorBehiviorMng = () => {
    const { node, setFocusNode, invalidate, setModify, isModify } = useContext(FactoryTreeContext);
    const mng = (node.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamManager;

    const [isVector, hasDecayParam, hasGravityParam] = useMemo(() => {
        const list = node.children.map(el => {
            return ((el.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamItem).name;
        });
        return [
            list.includes(ParamUtil.BehaviorParamType.VEC_X),
            list.includes(ParamUtil.ModulateParamType.DECAY_RATE),
            list.includes(ParamUtil.ModulateParamType.GRAVITY_VAL)
        ]
    }, [node]);

    const exchangeElements = (flag: boolean, listPos: number,
        trueParamElements: LogicUtil.NodeParamItem[],
        falseParamElements: LogicUtil.NodeParamItem[]
    ) => {
        const size = (flag ? trueParamElements : falseParamElements).length;
        LogicElementUtil.removeChild(node, listPos, size);

        (flag ? trueParamElements : falseParamElements).forEach((data, i) => {
            const wrap: LogicUtil.WrapElement = {
                type: 'param-item',
                data
            };
            LogicElementUtil.insertChild(wrap, node, listPos + i);
        });
        setFocusNode({ ...node });
        invalidate();
    };

    const exchangeVectorMethod = () => {
        exchangeElements(!isVector, 3,
            [
                { name: ParamUtil.BehaviorParamType.VEC_X, formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] },
                { name: ParamUtil.BehaviorParamType.VEC_Y, formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] },
            ],
            [
                { name: ParamUtil.BehaviorParamType.VEC_RAD, formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] },
                { name: ParamUtil.BehaviorParamType.VEC_LEN, formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] },
            ]
        )
    }

    /**
     * 
     */
    const toggleElements = (hasNode: boolean, listPos: number, paramElements: LogicUtil.NodeParamItem[]) => {
        const size = paramElements.length;
        if (hasNode) {
            mng.params.splice(listPos, size);
            node.children.splice(listPos, size);
            setFocusNode({ ...node });
            invalidate();
        } else {
            paramElements.forEach((data, i) => {
                const wrap: LogicUtil.WrapElement = {
                    type: 'param-item',
                    data
                };
                mng.params.splice(listPos + i, 0, wrap);
                const newNode = TreeUtil.createNode(wrap, node);
                node.children.splice(listPos + i, 0, newNode);
            });
            setFocusNode({ ...node });
            invalidate();
        }
    };

    return (<>
        <FormUtil.BorderPanel
            title="method"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Vector method'}
                            width={300}
                            isRelation
                            isUse={isVector}
                            callback={exchangeVectorMethod}
                        />
                    }
                />
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Direction method'}
                            width={300}
                            isRelation
                            isUse={!isVector}
                            callback={exchangeVectorMethod}
                        />
                    }
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="modulate"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Decay'}
                            width={300}
                            isUse={hasDecayParam}
                            callback={() => {
                                toggleElements(hasDecayParam, 5, [
                                    {
                                        name: ParamUtil.ModulateParamType.DECAY_RATE,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '0.9' }]
                                    },
                                    {
                                        name: ParamUtil.ModulateParamType.DECAY_LIMIT,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '1' }]
                                    },
                                ]);
                            }}
                        />
                    }
                />
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Gravity'}
                            width={300}
                            isUse={hasGravityParam}
                            callback={() => {
                                toggleElements(hasGravityParam, !hasDecayParam ? 5 : 7, [
                                    {
                                        name: ParamUtil.ModulateParamType.GRAVITY_VAL,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '0.2' }]
                                    },
                                    {
                                        name: ParamUtil.ModulateParamType.GRAVITY_LIMIT,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '10' }]
                                    },
                                ]);
                            }}
                        />
                    }
                />
            </>}
        />
    </>);
}

export default EditorBehiviorMng;