import { type } from "os";
import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../../utils/form/validateUtil";
import TreeUtil from "../../../../../utils/treeUtil";
import ConfirmDialog from "../../../../dialog/confirmDialog";
import { GlobalContext } from "../../../../entry/systemEntry";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicElementUtil from "../../util/logicElementUtil";
import LogicUtil from "../../util/logicUtil";
import ModifyOperations from "../common/modifyOperations";

const EditorFormgrpItem = () => {
    const { store, setStore } = useContext(GlobalContext);
    const { node, removeSelf, setModify, isModify, invalidate } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [groupMethod, setGroupMethod] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [targetList, setTargetList] = useState<FormUtil.CheckableValue[]>([]);

    const mappingForm = () => {
        const param = wrap.data as LogicUtil.NodeFromGrpItem;
        setName({ ...name, value: param.name });
        setGroupMethod({ ...groupMethod, value: param.method });
        setTargetList(param.args.map(arg => {
            return { value: arg, errors: [] };
        }));
    };

    /**  */
    const existNameList = useMemo(() => LogicElementUtil.getExistNameList(node), [node]);

    const seedargs = useMemo(() => {
        const argMng = LogicElementUtil.getInnerWrapFixed(node.parent?.parent?.data as LogicUtil.WrapElement, 'seedarg-mng').data as LogicUtil.NodeSeedArgManager;
        return argMng.args.map(wrap => (wrap.data as LogicUtil.NodeSeedArgItem).name);
    }, [node]);

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node]);

    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            checkableFields={[name, groupMethod].concat(targetList)}
            firstRegist={() => {
                const param: LogicUtil.NodeFromGrpItem = {
                    name: name.value,
                    method: groupMethod.value as LogicUtil.FromGrpMethod,
                    args: targetList.map(form => form.value)
                };
                wrap.data = param;
            }}
            modifyRegist={() => {
                const param = wrap.data as LogicUtil.NodeFromGrpItem;
                param.name = name.value;
                param.method = groupMethod.value as LogicUtil.FromGrpMethod;
                param.args = targetList.map(form => form.value);
                wrap.data = param;

            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Display"
                    jsx={
                        <FormUtil.TextField
                            width={270}
                            isReadOnly={!isModify}
                            checkable={name}
                            setCheckable={setName}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 1, 24),
                                    errorType: "length"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Method"
                    jsx={
                        <FormUtil.Combobox
                            checkable={groupMethod}
                            setCheckable={(checkable) => {
                                const type = checkable.value as LogicUtil.FromGrpMethod;
                                if (type === 'color') {
                                    setTargetList(new Array<FormUtil.CheckableValue>(3).fill({ value: '', errors: [] }));
                                } else {
                                    setTargetList([]);
                                }
                                setGroupMethod({...checkable});
                            }}
                            list={LogicUtil.FromGrpMedhods.map(type => {
                                return { value: type, labelText: type, isDisabled: type === 'list' }
                            })}
                            headBlank
                            width={200}
                            isReadOnly={!isModify}
                            validates={
                                [
                                    {
                                        checker: (value) => ValidateUtil.checkRequired(value),
                                        errorType: "required"
                                    }
                                ]
                            }
                        />
                    }
                />
                {targetList.map((target, i) => {
                    let formName = 'Target_';
                    if (groupMethod.value === 'color') {
                        switch (i) {
                            case 0: formName += 'R'; break;
                            case 1: formName += 'G'; break;
                            case 2: formName += 'B'; break;
                        }
                    } else {
                        formName += `_${i}`;
                    }
                    return (
                        <FormUtil.FormRecord
                            key={i}
                            titleLabel={formName}
                            jsx={
                                <FormUtil.Combobox
                                    checkable={target}
                                    setCheckable={(checkable) => {
                                        targetList[i] = checkable;
                                        setTargetList(targetList.slice());
                                    }}
                                    list={seedargs.map(item => {
                                        return { value: item, labelText: item }
                                    })}
                                    headBlank
                                    width={250}
                                    isReadOnly={!isModify}
                                    validates={
                                        [
                                            {
                                                checker: (value) => ValidateUtil.checkRequired(value),
                                                errorType: "required"
                                            },
                                            {
                                                checker: (value) => targetList.find((target, j) => i !== j && target.value === value) == undefined,
                                                errorType: "relation"
                                            }
                                        ]
                                    }
                                    dependency={targetList}
                                />
                            }
                        />
                    );
                })}
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.ButtonRecord
                    align="center"
                    buttons={[
                        {
                            label: 'Delete',
                            isEnable: !isModify,
                            callback: removeSelf
                        }
                    ]}
                />
                {/*  */}
                {LogicElementUtil.getSwapButtons(node, invalidate)}
            </>}
        />
    </>);
}

export default EditorFormgrpItem;