
namespace ParamUtil {
    export enum VisualParamType {
        RADIUS = 'radius',
        COLOR_R = 'color_r',
        COLOR_G = 'color_g',
        COLOR_B = 'color_b',
        COLOR_A = 'color_a',
        THICKNESS = 'thickness',
    }
    export const VisualParams = Object.values(VisualParamType);
    export type VisualParam = typeof VisualParams[number];

    export enum BehaviorParamType {
        LIMIT = 'limit',
        POS_X = 'pos_x',
        POS_Y = 'pos_y',
        VEC_X = 'vec_x',
        VEC_Y = 'vec_y',
        VEC_RAD = 'vec_radian',
        VEC_LEN = 'vec_length',
    }
    export const BehaviorParams = Object.values(BehaviorParamType);
    export type BehaviorParam = typeof BehaviorParams[number];

    export enum SelfParamType {
        FRAME = 'self_frame',
        LIMIT = 'self_limit',
        POS_X = 'self_pos_x',
        POS_Y = 'self_pos_y',
        VEC_X = 'self_vec_x',
        VEC_Y = 'self_vec_y',
        VEC_RAD = 'self_vec_radian',
        VEC_LEN = 'self_vec_length',
    }
    export const SelfParams = Object.values(SelfParamType);
    export type SelfParam = typeof SelfParams[number];

    export enum ExtendParamType {
        LOOP_COUNT = 'loop_count',
        LOOP_ITERATE = 'loop_iterate',
        INVOKE = 'invoke',
    }
    export const ExtendParams = Object.values(ExtendParamType);
    export type ExtendParam = typeof ExtendParams[number];

    export enum EffectParamType {
        DISAPPEAR_START = 'disappear_start',
        SHRINK_START = 'shrink_start',
        FLASH_APPEAR_FRAME = 'flash_appear_frame',
        FLASH_HIDE_FRAME = 'flash_hide_frame',
    }
    export const EffectParams = Object.values(EffectParamType);
    export type EffectParam = typeof EffectParams[number];

    export enum ModulateParamType {
        DECAY_RATE = 'decay_rate',
        DECAY_LIMIT = 'decay_limit',
        GRAVITY_VAL = 'gravity_val',
        GRAVITY_LIMIT = 'gravity_limit',
    }
    export const ModulateParams = Object.values(ModulateParamType);
    export type ModulateParam = typeof ModulateParams[number];

    export enum ConstantType {
        SCREEN_WIDTH = 'screen_width',
        SCREEN_HEIGHT = 'screen_height',
        PI = 'pi',
        RADIAN_ROUND = 'radian_round',
    }
    export const Constants = Object.values(ConstantType);
    export type Constant = typeof Constants[number];

}

export default ParamUtil;