import styled from "styled-components";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import { useContext, useEffect, useMemo, useState } from "react";
import { GlobalContext } from "../../../entry/systemEntry";


const LaunchEnvironment = (props: {
    isModify: boolean;
    cancelAction: () => void;
    modifyAction: () => void;
}) => {
    const { store, setStore } = useContext(GlobalContext);

    const [frame, setFrame] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [particle, setParticle] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });

    const data = store.launchData;

    const mapping = () => {
        setFrame({ ...frame, value: data.limitFrame.toString() });
        setParticle({ ...particle, value: data.limitParticle.toString() });
    }
    useEffect(() => {
        mapping();
    }, []);

    const isInputOK = particle.errors.length === 0 && frame.errors.length === 0;

    const maxLimit = data.defList.map(def => def.frame).reduce((prev, cur) => {
        return prev > cur ? prev : cur;
    }, 0);

    return (<_Wrap>
        <FormUtil.ButtonRecord
            align="center"
            buttons={[
                {
                    label: 'Cancel',
                    isEnable: props.isModify,
                    callback: () => {
                        mapping();
                        props.cancelAction();
                    }
                },
                {
                    label: !props.isModify ? 'Modify' : 'Regist',
                    isEnable: !props.isModify || isInputOK,
                    callback: () => {
                        data.limitFrame = Number(frame.value);
                        data.limitParticle = Number(particle.value);
                        setStore({ ...store });
                        props.modifyAction();
                    }
                }
            ]}
        />
        <FormUtil.BorderPanel
            title="Limit"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Frame"
                    jsx={
                        <FormUtil.TextField
                            width={100}
                            isReadOnly={!props.isModify}
                            isNumber
                            checkable={frame}
                            setCheckable={setFrame}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkNumberRange(value, maxLimit, 1000),
                                    errorType: "value"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Particle"
                    jsx={
                        <FormUtil.TextField
                            width={100}
                            isReadOnly={!props.isModify}
                            isNumber
                            checkable={particle}
                            setCheckable={setParticle}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkNumberRange(value, 100, 5000),
                                    errorType: "value"
                                }
                            ]}
                        />
                    }
                />
            </>}
        />
    </_Wrap>)
}

export default LaunchEnvironment;


const _Wrap = styled.div`
    display: inline-block;
    position: relative;
    background-color: #d1d1d1;
    width: 100%;
    height: 100%;
`;