import { createContext, useCallback, useEffect, useReducer, useState } from 'react';
import styled, { css } from 'styled-components';
import { initialStore, Store } from '../../redux/store/store';
import { MAIN_FRAME_MARGIN, TOTAL_HEADER_WIDTH } from '../../utils/systemConst';
import { HashRouter, Redirect, Route, Switch, useLocation } from "react-router-dom";
import GloballDialog from '../dialog/globalDialog';
import MobileRoot from '../mobile/mobileRoot';
import MainFrame from './mainFrame';
import Splash from './splash';
import TotalHeader from './totalHeader';
import DirectView from './directView';
import DirectLogin from './directLogin';


type GlobalContextType = {
    store: Store;
    setStore: React.Dispatch<React.SetStateAction<Store>>;
}

export const GlobalContext = createContext({} as GlobalContextType);

const SystemEntry = () => {

    // const { store, setStore } = useContext(GlobalContext);

    return (
        <HashRouter basename={process.env.PUBLIC_URL}>
            <RouterManage />
        </HashRouter>
    );
}

export default SystemEntry;

const RouterManage = () => {

    const [store, setStore] = useState(initialStore);

    const query = new URLSearchParams(useLocation().search);
    const str = query.get('v') as string;

    const splashJsx = !store.system.isSplash ? <></> : (
        <Splash
            remove={() => {
                setStore({
                    ...store, system: {
                        ...store.system, isSplash: false
                    }
                });
            }}
        />
    );
    return (
        <GlobalContext.Provider value={{ store, setStore }}>
            <Switch>
                <Route path="/" exact>
                    {splashJsx}
                    <_Wrap>
                        <GloballDialog />
                        <TotalHeader />
                        <_MainDiv>
                            <MainFrame />
                        </_MainDiv>
                    </_Wrap>
                </Route>
                <Route path="/test" exact >
                    <_Wrap>
                    </_Wrap>
                </Route>
                <Route path="/preview" exact >
                    <MobileRoot hashKey={str} />
                </Route>
                <Route path="/user" exact >
                    <DirectLogin hashKey={str} />
                </Route>
            </Switch>
        </GlobalContext.Provider>
    );

}

const _Wrap = styled.div`
    display: inline-block;
    position: relative;
    min-width: 1200px;
    min-height: 900px;
    width: 100%;
    height: 100%;
    background-color: #364887;
`;

const _MainDiv = styled.div`
    display: inline-block;
    width: 100%;
    height: calc(100% - ${TOTAL_HEADER_WIDTH}px);
    /* background-color: #c2c2c2; */
`;