import { useContext, useEffect, useMemo, useState } from "react";
import styled from "styled-components";
import CanvasUtil from "../../../../../utils/canvasUtil";
import FormUtil from "../../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../../utils/form/validateUtil";
import TreeUtil from "../../../../../utils/treeUtil";
import Particle from "../../../../graphic/particle";
import PreviewUtil from "../../../preview/previewUtil";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicElementUtil from "../../util/logicElementUtil";
import LogicUtil from "../../util/logicUtil";
import ModifyOperations from "../common/modifyOperations";

const WND_SIZE = 100;

const EditorThumbnailItem = () => {
    const { rootData, node, removeSelf, setModify, isModify, invalidate } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;
    const data = wrap.data as LogicUtil.NodeThumbnailItem;


    const seedWrap = node.parent?.parent?.data as LogicUtil.WrapElement;
    const sampleMng = LogicElementUtil.getInnerWrap(seedWrap, 'sample-mng')?.data as LogicUtil.NodeSampleManager;
    const samples = sampleMng.samples.map(wrap => wrap.data as LogicUtil.NodeSampleItem);

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [frame, setFrame] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [urls, setUrls] = useState<string[]>([]);

    /**  */
    const reservedList = useMemo(() => {
        const owner = (node.parent?.parent?.data as LogicUtil.WrapElement).data;
        return LogicElementUtil.getParamNameList(owner);
    }, [node]);

    const mappingForm = () => {
        const param = wrap.data as LogicUtil.NodeThumbnailItem;
        setName({ ...name, value: param.name });
        setFrame({ ...frame, value: param.frame.toString() });
    };
    
    /**  */
    const existNameList = useMemo(() => LogicElementUtil.getExistNameList(node), [node]);

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
            setTimeout(() => {
                const urls: string[] = [];
                samples.forEach((sample, i) => {
                    urls.push(getThumbnailURL(data.frame, i));
                });
                setUrls(urls);
            }, 1);
        }
    }, [node]);

    const getSeedValueMap = (sampleNo: number): PreviewUtil.ValueMap[] => {
        const argMng = LogicElementUtil.getInnerWrap(seedWrap, 'interface-mng', 'seedarg-mng')?.data as LogicUtil.NodeSeedArgManager;
        const sendMng = LogicElementUtil.getInnerWrap(seedWrap, 'interface-mng', 'seedsend-mng')?.data as LogicUtil.NodeSeedSendManager;

        const argMap: PreviewUtil.ValueMap[] = argMng.args.map((arg, i) => {
            const name = (arg.data as LogicUtil.NodeSeedArgItem).name;
            const value = samples[sampleNo].values[i];
            return { name, value }
        });
        const sendParams = sendMng.params.map(send => send.data as LogicUtil.NodeParamItem);
        return sendParams.map(item => {
            return { name: item.name, value: PreviewUtil.convertParamsToValue(item.name, sendParams, argMap) ?? 0 }
        });
    }

    const getThumbnailURL = (targetFrame: number, sampleNo: number) => {
        console.log('create start!');
        const owner: Particle.Instance[] = [];

        let frameCnt = 0;
        do {
            if (frameCnt === 0) {
                const seed = (node.parent?.parent?.data as LogicUtil.WrapElement).data as LogicUtil.NodeSeedItem;
                const logicManager = (rootData.data as LogicUtil.NodeRoot).logicMng.data as LogicUtil.NodeLogicManager;
                const logic = logicManager.defs.find(logic => (logic.data as LogicUtil.NodeLogicItem).name === seed.entry);
                if (logic == undefined) break;

                const sendValueMapList = getSeedValueMap(sampleNo);
                owner.push(new Particle.Instance(
                    PreviewUtil.buildLogicProps(owner, logic.data, rootData, sendValueMapList))
                );
            }

            if (frameCnt === targetFrame) {
                return CanvasUtil.createURL(550, 550, (ctx) => {

                    // 
                    owner.forEach(par => {
                        par.draw(ctx);
                    });
                    ctx.save();
                });
            }

            // 
            for (let i = owner.length - 1; i >= 0; i--) {
                const particle = owner[i];

                particle.update();
            }

            // 
            for (let i = owner.length - 1; i >= 0; i--) {
                const particle = owner[i];
                if (!particle.isLive) {
                    owner.splice(i, 1);
                }
            }

            if (frameCnt > 500 || owner.length > 3000) break;
            frameCnt++;
        } while (owner.length > 0);
        return '';
    }

    const pictureJsxList = useMemo(() => {
        const sampleMng = LogicElementUtil.getInnerWrap((node.parent?.parent as TreeUtil.ElementNode).data, 'sample-mng')?.data as LogicUtil.NodeSampleManager;
        return sampleMng.samples.map((wrap, i) => {
            const sample = wrap.data as LogicUtil.NodeSampleItem;

            const url = urls[i];
            const imgJsx = url == undefined ? <></> : <_IMG src={urls[i]} />;
            return (
                <_SampleDiv key={i}>-
                    <_SampleName>{sample.name}</_SampleName>
                    <_CanvasDiv>
                        {imgJsx}
                    </_CanvasDiv>
                </_SampleDiv>
            );
        });
    }, [node, urls]);

    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            checkableFields={[name, frame]}
            firstRegist={() => {
                const param: LogicUtil.NodeThumbnailItem = {
                    name: name.value,
                    frame: Number(frame.value),
                };
                wrap.data = param;
            }}
            modifyRegist={() => {
                const param = wrap.data as LogicUtil.NodeThumbnailItem;
                param.name = name.value;
                param.frame = Number(frame.value);
                wrap.data = param;
            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Name"
                    jsx={
                        <FormUtil.TextField
                            width={200}
                            isReadOnly={!isModify}
                            checkable={name}
                            setCheckable={setName}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 1, 16),
                                    errorType: "length"
                                },
                                {
                                    checker: (value) => LogicUtil.checkFieldName(value),
                                    errorType: "value"
                                },
                                {
                                    checker: (value) => !existNameList.includes(value),
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Frame"
                    jsx={
                        <FormUtil.TextField
                            width={100}
                            isReadOnly={!isModify}
                            isNumber
                            checkable={frame}
                            setCheckable={setFrame}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                }
                            ]}
                        />
                    }
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="preview"
            isEnable={!isModify}
            innerJsx={<>
                {pictureJsxList}
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.ButtonRecord
                    align="center"
                    buttons={[
                        {
                            label: 'Delete',
                            isEnable: !isModify,
                            callback: () => {
                                removeSelf();
                            }
                        }
                    ]}
                />
                {/*  */}
                {LogicElementUtil.getSwapButtons(node, invalidate)}
            </>}
        />
    </>);
}

export default EditorThumbnailItem;

const _SampleDiv = styled.div<{}>`
    display: inline-block;
    /* width: ${WND_SIZE}px;
    height: ${WND_SIZE}px; */
    margin: 0 0 0 5px;
`;
const _SampleName = styled.div<{}>`
    display: block;
    color: #09056c;
    font-size: 14px;
    font-weight: 600;
    font-style: italic;
    background-color: #ffffff5e;
`;
const _CanvasDiv = styled.div<{}>`
    display: inline-block;
    width: ${WND_SIZE}px;
    height: ${WND_SIZE}px;
    background-color: #020202df;
    border: solid 2px #ca0000;
    box-sizing: border-box;
`;

const _IMG = styled.img`
    display: inline-block;
    position: relative;
    width: 100%;
    height: 100%;
`;