import { useContext } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import TreeUtil from "../../../../../utils/treeUtil";
import LogicMainteTab, { FactoryTreeContext } from "../../logicMainteTab";
import LogicUtil from "../../util/logicUtil";

const EditorInheritConnMng = () => {
    const { node, setFocusNode, invalidate } = useContext(FactoryTreeContext);
    const mng = (node.data as LogicUtil.WrapElement).data as LogicUtil.NodeConnectManager;

    return (<>
        <FormUtil.BorderPanel
            title="operation"
            innerJsx={<FormUtil.ButtonRecord
                align="center"
                buttons={[
                    {
                        label: 'Add Inherit',
                        callback: () => {
                            const wrap: LogicUtil.WrapElement = {
                                type: 'connector',
                                data: null
                            }
                            mng.inherits.push(wrap);
                            const newNode = TreeUtil.createNode(wrap, node);
                            node.children.push(newNode);
                            setFocusNode(newNode);
                            invalidate();
                        }
                    }
                ]}
            />}
        />
    </>);
}

export default EditorInheritConnMng;