import { useContext, useMemo } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import TreeUtil from "../../../../../utils/treeUtil";
import { FactoryTreeContext as LogicTreeContext } from "../../logicMainteTab";
import LogicElementUtil from "../../util/logicElementUtil";
import LogicUtil from "../../util/logicUtil";

const EditorThumbnailMng = () => {
    const { node, setFocusNode, invalidate } = useContext(LogicTreeContext);
    const mng = (node.data as LogicUtil.WrapElement).data as LogicUtil.NodeThumnailManager;

    const hasSample = useMemo(() => {
        const sampleMngNode = LogicElementUtil.getInnerNode(node.parent as TreeUtil.ElementNode, 'sample-mng') as TreeUtil.ElementNode;
        return sampleMngNode.children.length as number > 0;
    }, []);

    return (<>
        <FormUtil.BorderPanel
            title="operation"
            isEnable={hasSample}
            innerJsx={<FormUtil.ButtonRecord
                align="center"
                buttons={[
                    {
                        label: 'Add Thumbnail',
                        callback: () => {
                            const newWrap: LogicUtil.WrapElement = {
                                type: 'thumbnail-item',
                                data: null
                            }
                            const newNode = LogicElementUtil.addChild(newWrap, node);
                            setFocusNode(newNode);
                        }
                    }
                ]}
            />}
        />
    </>);
}

export default EditorThumbnailMng;