import { useContext, useMemo } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import TreeUtil from "../../../../../utils/treeUtil";
import ConfirmDialog from "../../../../dialog/confirmDialog";
import { GlobalContext } from "../../../../entry/systemEntry";
import LogicTemplateDialog from "../../dialog/logicTemplateDialog";
import SendTemplateDialog from "../../dialog/sendTemplateDialog";
import LogicMainteTab, { FactoryTreeContext } from "../../logicMainteTab";
import LogicElementUtil from "../../util/logicElementUtil";
import LogicUtil from "../../util/logicUtil";

const EditorInterfaceManager = () => {
    const { store, setStore } = useContext(GlobalContext);
    const { rootData, node, setFocusNode, invalidate } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    const argMngNode = LogicElementUtil.getInnerNodeFixed(node, 'seedarg-mng');
    const sendMngNode = LogicElementUtil.getInnerNodeFixed(node, 'seedsend-mng');
    const sampleMngNode = LogicElementUtil.getInnerNodeFixed(node.parent as TreeUtil.ElementNode, 'sample-mng');

    const [hasGroupNode] = useMemo(() => {
        const list = node.children.map(el => {
            return (el.data as LogicUtil.WrapElement).type;
        });
        return [
            list.includes('formgrp-mng'),
        ];
    }, [node]);

    const hasArgument = (): boolean => {
        return argMngNode.children.length > 0;
    }

    /**
     * 
     */
    const hasGroup = () => {
        const groupMngNode = LogicElementUtil.getInnerNode(node, 'formgrp-mng');
        if (groupMngNode == null) return false;
        return groupMngNode.children.length > 0;
    }

    const execute = () => {
        // const argMng = (argMngNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeSeedArgManager;
        // 
        if (hasArgument()) {

            LogicElementUtil.removeChildAll(argMngNode);
            LogicElementUtil.removeChildAll(sampleMngNode);
        }

        sendMngNode.children.forEach(child => {
            const send = (child.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamItem;
            send.formulaItems = ['var'];
            send.values = [{ type: 'argument', value: send.name }];

            const newArgData: LogicUtil.NodeSeedArgItem = {
                name: send.name,
                step: 1,
                min: -9999,
                max: 9999,
                defval: 0
            }
            const newArgWrap: LogicUtil.WrapElement = {
                type: 'seedarg-item',
                data: newArgData
            };
            const argMng = (argMngNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeSeedArgManager;
            argMng.args.push(newArgWrap);
            argMngNode.children.push(TreeUtil.createNode(newArgWrap, argMngNode));
            invalidate();
        });
    }

    const initDefaultSetting = () => {
        if (!hasArgument()) {
            execute();
        } else {
            store.system.dialog = <ConfirmDialog.Component
                message="Are you sure you want to delete the existing element?"
                buttons={[
                    {
                        label: 'Cancel',
                        callback: () => {
                            store.system.dialog = null;
                            setStore({ ...store });
                        }
                    },
                    {
                        label: 'Execute',
                        callback: () => {
                            execute();
                            store.system.dialog = null;
                            setStore({ ...store });
                        }
                    }
                ]}
            />
            setStore({ ...store });
        }
        invalidate();
    }

    /**
     * 
     */
    const toggleElement = (hasNode: boolean, listPos: number, wrapElement: LogicUtil.WrapElement) => {
        if (hasNode) {
            LogicElementUtil.removeChild(node, listPos, 1);
            setFocusNode({ ...node });
            invalidate();
        } else {
            LogicElementUtil.insertChild(wrapElement, node, listPos);
            setFocusNode({ ...node });
            invalidate();
        }
    };

    return (<>
        <FormUtil.BorderPanel
            title="element"
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={<FormUtil.Switch
                        label={'Group'}
                        width={300}
                        isUse={hasGroupNode}
                        isEnable={!hasGroup()}
                        callback={() => {
                            let listPos = 0;
                            toggleElement(
                                hasGroupNode,
                                listPos,
                                {
                                    type: 'formgrp-mng',
                                    data: {
                                        defines: []
                                    } as LogicUtil.NodeFromgrpManager
                                }
                            );
                        }}
                    />}
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            innerJsx={<FormUtil.ButtonRecord
                align="center"
                buttons={[
                    {
                        label: 'Initialize Params',
                        callback: initDefaultSetting
                    }
                ]}
            />}
        />
    </>);
}

export default EditorInterfaceManager;