import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../../utils/form/validateUtil";
import TreeUtil from "../../../../../utils/treeUtil";
import { GlobalContext } from "../../../../entry/systemEntry";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicUtil from "../../util/logicUtil";
import ModifyOperations from "../common/modifyOperations";
import LogicElementUtil from "../../util/logicElementUtil";

const EditorSeedItem = () => {
    const { store, setStore } = useContext(GlobalContext);
    const { removeSelf, node, rootData, invalidate, setModify, isModify } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [disp, setDisp] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [entry, setEntry] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [permit, setPermit] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });

    const mappingForm = () => {
        const param = wrap.data as LogicUtil.NodeSeedItem;
        setName({ ...name, value: param.name });
        setDisp({ ...name, value: param.disp ?? '' });
        setEntry({ ...entry, value: param.entry });
        setPermit({ ...permit, value: param.permit })
    };

    /**  */
    const existNameList = useMemo(() => LogicElementUtil.getExistNameList(node), [node]);

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node]);

    // 
    const logicNameList = useMemo((): string[] => {
        const rootNode = rootData.data as LogicUtil.NodeRoot;
        const logicMng = rootNode.logicMng.data as LogicUtil.NodeLogicManager;
        return logicMng.defs.map(wrap => {
            return (wrap.data as LogicUtil.NodeLogicItem).name;
        });
    }, [node]);

    const getSendParamList = (): string[] => {
        const seed = wrap.data as LogicUtil.NodeSeedItem;
        const logicMng = (rootData.data as LogicUtil.NodeRoot).logicMng.data as LogicUtil.NodeLogicManager;
        const dependItem = logicMng.defs.find(wrap => {
            return (wrap.data as LogicUtil.NodeLogicItem).name === seed.entry;
        });
        if (dependItem != undefined) {
            const logicWrap = dependItem;
            const argWrap = (logicWrap.data as LogicUtil.NodeLogicItem).mngs[0];
            if (argWrap.type === 'argument-mng') {
                return (argWrap.data as LogicUtil.NodeArgumentManager).args.map(arg => {
                    return (arg.data as LogicUtil.NodeArgumentItem).name;
                });
            }
        }
        return [];
    }

    const initSendParams = () => {
        const sendParamList = getSendParamList();
        const sendNode = LogicElementUtil.getInnerNode(node, 'interface-mng', 'seedsend-mng') as TreeUtil.ElementNode;
        const params = ((sendNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamManager).params;
        // 
        const curParamList = params.map(param => {
            return (param.data as LogicUtil.NodeParamItem).name;
        });
        sendParamList.forEach(param => {
            if (!curParamList.includes(param)) {
                const paramWrap = LogicElementUtil.createParamWrap('seedsend-item',
                    { name: param, formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] }
                );
                params.push(paramWrap);
            }
        });
        LogicElementUtil.syncTreeElementFromWrap(sendNode);
    }

    const getDefaultSeedMngs = (): LogicUtil.WrapElement[] => {
        return [
            {
                type: 'interface-mng',
                data: {
                    mngs: [
                        {
                            type: 'seedarg-mng',
                            data: {
                                args: []
                            } as LogicUtil.NodeSeedArgManager
                        },
                        {
                            type: 'seedsend-mng',
                            data: {
                                params: []
                            } as LogicUtil.NodeSeedSendManager
                        },
                    ]
                } as LogicUtil.NodeInterfaceMng
            },
            {
                type: 'thumbnail-mng',
                data: {
                    items: []
                } as LogicUtil.NodeThumnailManager
            },
            {
                type: 'sample-mng',
                data: {
                    samples: []
                } as LogicUtil.NodeSampleManager
            }
        ];
    }

    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            checkableFields={[name, disp, entry, permit]}
            firstRegist={() => {
                const element: LogicUtil.NodeSeedItem = {
                    name: name.value,
                    disp: disp.value === '' ? undefined : disp.value,
                    entry: entry.value,
                    permit: permit.value as LogicUtil.PermitType,
                    mngs: getDefaultSeedMngs()
                };
                wrap.data = element;

                LogicElementUtil.syncTreeElementFromWrap(node);
                // Send
                initSendParams();
                invalidate();
                setStore({ ...store });
            }}
            modifyRegist={() => {
                const param = wrap.data as LogicUtil.NodeSeedItem;
                param.name = name.value;
                param.disp = disp.value === '' ? undefined : disp.value;
                param.permit = permit.value as LogicUtil.PermitType;
                wrap.data = param;
            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Name"
                    jsx={
                        <FormUtil.TextField
                            width={200}
                            isReadOnly={!isModify}
                            checkable={name}
                            setCheckable={setName}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 1, 16),
                                    errorType: "length"
                                },
                                {
                                    checker: (value) => LogicUtil.checkFieldName(value),
                                    errorType: "value"
                                },
                                {
                                    checker: (value) => !existNameList.includes(value),
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Display"
                    jsx={
                        <FormUtil.TextField
                            width={270}
                            isReadOnly={!isModify}
                            checkable={disp}
                            setCheckable={setDisp}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 0, 24),
                                    errorType: "length"
                                },
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Entry"
                    jsx={
                        <FormUtil.Combobox
                            marginLeft={2}
                            checkable={entry}
                            width={200}
                            isReadOnly={!isModify}
                            setCheckable={setEntry}
                            list={logicNameList.map(item => {
                                return { value: item, labelText: item };
                            })}
                            headBlank
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Permit"
                    jsx={
                        <FormUtil.Combobox
                            marginLeft={2}
                            checkable={permit}
                            width={178}
                            isReadOnly={!isModify}
                            setCheckable={setPermit}
                            list={LogicUtil.PermitTypes.map(item => {
                                return { value: item, labelText: item };
                            })}
                            headBlank
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                }
                            ]}
                        />
                    }
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            isEnable={!isModify}
            innerJsx={<>
                {LogicElementUtil.getReplicateButton(node, !isModify, 'seed', store, setStore, invalidate)}
                <FormUtil.ButtonRecord
                    align="center"
                    buttons={[
                        {
                            label: 'Delete',
                            callback: () => {
                                removeSelf();
                            }
                        }
                    ]}
                />
                {/*  */}
                {LogicElementUtil.getSwapButtons(node, invalidate)}
            </>}
        />
    </>);
}

export default EditorSeedItem;