import { useContext, useEffect, useState } from "react";
import styled, { css } from "styled-components";
import Styles, { _CSS_LABEL_MIDIUM } from "../../base/design/styles";
import DataUtil from "../../utils/dataUtil";
import { HOME_USER_DIV_HEIGHT } from "../../utils/systemConst";
import { GlobalContext } from "../entry/systemEntry";
import PublishFrame from "./publishFrame";
import ReleaseNote from "./releaseNote";

type scoreProps = {
    dispnam: string;
    seq: number;
    owner: string;
    lastmod: string;
}

const HomeChannel = () => {

    const { store, setStore } = useContext(GlobalContext);

    const [userCount, setUserCount] = useState(0);
    const [artsCnt, setArtsCnt] = useState(0);

    useEffect(() => {
        countUsers().then(cnt => {
            setUserCount(cnt);
        });
    }, []);


    return (<_Wrap>
        <_Left>
            <_MessageRecord>{userCount} users have created accounts.</_MessageRecord>
            <ReleaseNote />
        </_Left>
        <_Right>
            <_MessageRecord>{artsCnt} libruary have been published.</_MessageRecord>
            <PublishFrame setLibCnt={setArtsCnt}/>
        </_Right>
    </_Wrap>);
}

export default HomeChannel;

export const countUsers = async () => {
    const response = await DataUtil.sendQueryRequestToAPI('select', `SELECT count(*) cnt FROM user_tbl`);
    const results = await response.json();
    return (results as any[])[0]['cnt'] as number;
};

export const findAllReleaseProjectList = async () => {
    const response = await DataUtil.sendQueryRequestToAPI('select', `
        SELECT seq, (select id from user_tbl where seq = prj.seq) as owner, source, lastmod
        FROM prjcttbl prj
        ORDER BY lastmod desc
    `);
    const results = await response.json();
    return results as any[];
};

const _Wrap = styled.div`
    display: inline-block;
    width: calc(100% - 8px);
    height: calc(100% - 8px);
    margin: 4px 0 0 4px;
    background-color: #ccc;
`;

const _Left = styled.div`
    display: inline-block;
    width: 50%;
    height: 100%;
    background-color: #bbcac7;
`;


const _MessageRecord = styled.div`
    display: inline-block;
    width: 100%;
    height: 40px;
    font-size: 22px;
    font-weight: 600;
    color: #cd0202;
    background-color: #ffffff3d;
    padding: 0 0 0 10px;
    box-sizing: border-box;
`;


const _Right = styled.div`
    display: inline-block;
    width: 50%;
    height: 100%;
    background-color: #bbbfca;
    vertical-align: top;
`;