import { useContext, useEffect, useMemo } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicElementUtil from "../../util/logicElementUtil";
import LogicUtil from "../../util/logicUtil";
import ParamUtil from "../../util/paramUtil";

const EditorExtendMng = () => {
    const { node, setFocusNode, invalidate } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    const [hasLoopParam, hasInvokeParam] = useMemo(() => {
        const list = node.children.map(el => {
            return ((el.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamItem).name as ParamUtil.ExtendParam;
        });
        return [
            list.includes(ParamUtil.ExtendParamType.LOOP_COUNT),
            list.includes(ParamUtil.ExtendParamType.INVOKE)
        ]
    }, [node]);

    useEffect(() => {
    }, [node]);

    /**
     * 
     */
    const toggleElement = (hasNode: boolean, listPos: number, wrapElement: LogicUtil.WrapElement) => {
        if (hasNode) {
            LogicElementUtil.removeChild(node, listPos, 1);
            setFocusNode({ ...node });
            invalidate();
        } else {
            LogicElementUtil.insertChild(wrapElement, node, listPos);
            setFocusNode({ ...node });
            invalidate();
        }
    };

    return (<>
        <FormUtil.BorderPanel
            title="element"
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Loop'}
                            width={300}
                            isUse={hasLoopParam}
                            callback={() => {
                                toggleElement(hasLoopParam, 0, {
                                    type: 'param-item',
                                    data: {
                                        name: ParamUtil.ExtendParamType.LOOP_COUNT,
                                        formulaItems: ['var'],
                                        values: [{ type: 'direct', value: '1' }]
                                    } as LogicUtil.NodeParamItem
                                });
                            }}
                        />
                    }
                />
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Invoke'}
                            width={300}
                            isUse={hasInvokeParam}
                            callback={() => {
                                toggleElement(hasInvokeParam, hasLoopParam ? 1 : 0, {
                                    type: 'condition-item',
                                    data: {
                                        name: ParamUtil.ExtendParamType.INVOKE,
                                        formulaItems: ['var', '=', 'var'],
                                        values: [{ type: 'direct', value: '0' }, { type: 'direct', value: '0' }]
                                    } as LogicUtil.NodeParamItem
                                });
                            }}
                        />
                    }
                />
            </>}
        />
    </>);
}

export default EditorExtendMng;