import DataUtil from "../../utils/dataUtil";
import PreviewLogicFrame from "../create/preview/previewLogicFrame";
import { useContext, useEffect, useMemo, useState, useRef } from "react";
import LogicUtil from "../create/factory/util/logicUtil";
import LogicElementUtil from "../create/factory/util/logicElementUtil";
import styled from "styled-components";

type LibProps = {
    name: string;
    ownerId: string;
    lastmod: string;
    rootData: LogicUtil.WrapElement;
    isOpen: boolean;
    seedList: SeedProps[];
}
type SeedProps = {
    name: string;
    disp?: string;
    argCount: number;
    master: LogicUtil.WrapElement;
}

const DirectLinkManager = (props: {
    libSeq: number;
}) => {
    const [libData, setLibData] = useState<LibProps | null>(null);

    const [seedSeq, setSeedSeq] = useState(-1);

    useEffect(() => {

        findSeed(props.libSeq).then(resList => {
            if (resList.length > 0) {
                const res = resList[0];
                const name = res.name;
                const ownerId = res.owner;
                const lastmod = res.lastmod;
                const rootData = JSON.parse(DataUtil.unZip(res.source)) as LogicUtil.WrapElement;
                const seedMng = (rootData.data as LogicUtil.NodeRoot).seedMng.data as LogicUtil.NodeSeedManager;
                const seedList: SeedProps[] = seedMng.defs
                    .filter(wrap => {
                        const seed = wrap.data as LogicUtil.NodeSeedItem;
                        return seed.permit === 'release';
                    })
                    .map(wrap => {
                        const seed = wrap.data as LogicUtil.NodeSeedItem;
                        const argMng = LogicElementUtil.getInnerWrapFixed(wrap, 'interface-mng', 'seedarg-mng');
                        const argCount = (argMng.data as LogicUtil.NodeSeedArgManager).args.length;
                        return { name: seed.name, disp: seed.disp, argCount, master: wrap };
                    });
                setLibData({
                    name, ownerId, lastmod, rootData, seedList, isOpen: false
                });
                console.log(seedList.length);
            }
        });
    }, []);

    const getJsx = () => {
        let jsx = <></>;
        if (libData != null) {
            if (seedSeq === -1) {
                jsx = <>
                <_Header>{libData.name}</_Header>
                    {libData.seedList.map((seed, i) => {
                        const seedName = seed.disp ?? seed.name;
                        return (<_Item key={i}
                            onClick={() => {
                                setSeedSeq(i);
                            }}
                        >
                            <_SeedName>{seedName}</_SeedName>
                            <_ArgCnt> [{seed.argCount} arguments]</_ArgCnt>
                        </_Item>);
                    })}
                </>;
            } else {
                jsx = <PreviewLogicFrame.Component
                    master={libData.seedList[seedSeq].master}
                    rootData={libData.rootData}
                    closeButtonAction={() => {
                        setSeedSeq(-1);
                    }}
                />
            }
        }
        return jsx;
    }

    return getJsx();
}

export default DirectLinkManager;


const findSeed = async (conteseq: number) => {
    const response = await DataUtil.sendQueryRequestToAPI('select', `
        SELECT name, (select id from user_tbl where seq = prj.owner) as owner, source, lastmod
        FROM prjcttbl prj
        WHERE seq = ${conteseq}
    `);
    const results = await response.json();
    return results as any[];
};

const _Header = styled.div`
    display: inline-block;
    position: relative;
    width: 100%;
    height: 50px;
    background: linear-gradient(to bottom, #00215e, #000000, #00215e);

    color: #e0e0e0;
    font-size: 28px;
    font-wieght: 600;
    line-height: 48px;
    padding-left: 8px;
    box-sizing: border-box;
`;

const _Item = styled.div`
    display: inline-block;
    position: relative;
    width: 100%;
    height: 40px;
    background: linear-gradient(to bottom, #192f7e, #000540, #192f7e);

    font-size: 24px;
    font-wieght: 600;
    line-height: 38px;
    padding-left: 8px;
    box-sizing: border-box;
`;

const _SeedName = styled.span<{
}>`
    color: #abc9e6;
`;

const _ArgCnt = styled.span<{
}>`
    color: #fbffbf;
`;