import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicElementUtil from "../../util/logicElementUtil";
import LogicUtil from "../../util/logicUtil";
import ParamUtil from "../../util/paramUtil";
import ModifyOperations from "../common/modifyOperations";

type Medhod = 'blank' | 'particle' | 'snake'

const EditorVisualMng = () => {
    const { node, setFocusNode, invalidate, setModify, isModify } = useContext(FactoryTreeContext);
    const mng = (node.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamManager;

    const [drawMethod, hasDisappearParam, hasShrinkParam, hasFlashParam] = useMemo(() => {
        const list = node.children.map(el => {
            return ((el.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamItem).name;
        });
        let drawMethod: Medhod = 'blank';
        if(list.length > 0) {
            drawMethod = list.includes(ParamUtil.VisualParamType.THICKNESS) ? 'snake' : 'particle';
        }
        return [
            drawMethod,
            list.includes(ParamUtil.EffectParamType.DISAPPEAR_START),
            list.includes(ParamUtil.EffectParamType.SHRINK_START),
            list.includes(ParamUtil.EffectParamType.FLASH_APPEAR_FRAME)
        ]
    }, [node]);

    /**
     * 
     */
    const toggleElements = (hasNode: boolean, listPos: number, paramElements: LogicUtil.NodeParamItem[]) => {
        const size = paramElements.length;
        if (hasNode) {
            LogicElementUtil.removeChild(node, listPos, size);
            setFocusNode({ ...node });
            invalidate();
        } else {
            paramElements.forEach((data, i) => {
                const wrap: LogicUtil.WrapElement = {
                    type: 'param-item',
                    data
                };
                LogicElementUtil.insertChild(wrap, node, listPos + i);
            });
            setFocusNode({ ...node });
            invalidate();
        }
    };

    const addElements = (paramElements: LogicUtil.NodeParamItem[]) => {
        const size = paramElements.length;
        paramElements.forEach((data, i) => {
            const wrap: LogicUtil.WrapElement = {
                type: 'param-item',
                data
            };
            LogicElementUtil.insertChild(wrap, node, i);
        });
        setFocusNode({ ...node });
        invalidate();
    };
    const removeElements = (elementNameList: string[]) => {
        const mng = (node.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamManager;
        elementNameList.forEach((name) => {
            mng.params.forEach((param, i) => {
                if((param.data as LogicUtil.NodeParamItem).name === name) {
                    LogicElementUtil.removeChild(node, i, 1);
                }
            });
        });
        setFocusNode({ ...node });
        invalidate();
    };

    return (<>
        <FormUtil.BorderPanel
            title="method"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    isEnable={!(hasShrinkParam || hasDisappearParam || hasFlashParam)}
                    jsx={
                        <FormUtil.Switch
                            label={'None'}
                            width={300}
                            isRelation
                            isUse={drawMethod === 'blank'}
                            callback={() => {
                                mng.params.length = 0;
                                node.children.length = 0;
                                setFocusNode({ ...node });
                                invalidate();
                            }}
                        />
                    }
                />
                {/* <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Snake Method'}
                            width={300}
                            isRelation
                            isUse={drawMethod === 'snake'}
                            callback={() => {
                                removeElements([
                                    ParamUtil.VisualParamType.RADIUS
                                ]);
                                addElements([
                                    {
                                        name: ParamUtil.VisualParamType.THICKNESS,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '10' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_R,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_G,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_B,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_A,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                ]);
                            }}
                        />
                    }
                /> */}
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Particle Method'}
                            width={300}
                            isRelation
                            isUse={drawMethod === 'particle'}
                            callback={() => {
                                addElements([
                                    {
                                        name: ParamUtil.VisualParamType.RADIUS,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '20' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_R,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_G,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_B,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_A,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                ]);
                            }}
                        />
                    }
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="effect"
            isEnable={!isModify && drawMethod !== 'blank'}
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    isEnable={!hasFlashParam}
                    jsx={
                        <FormUtil.Switch
                            label={'Disappear'}
                            width={300}
                            isUse={hasDisappearParam}
                            callback={() => {
                                toggleElements(hasDisappearParam, 5, [
                                    {
                                        name: ParamUtil.EffectParamType.DISAPPEAR_START,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '0' }]
                                    },
                                ]);
                            }}
                        />
                    }
                />
                <FormUtil.Record
                    textAlign="center"
                    isEnable={!hasFlashParam}
                    jsx={
                        <FormUtil.Switch
                            label={'Shrink'}
                            width={300}
                            isUse={hasShrinkParam}
                            callback={() => {
                                toggleElements(hasShrinkParam, !hasDisappearParam ? 5 : 6, [
                                    {
                                        name: ParamUtil.EffectParamType.SHRINK_START,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '0' }]
                                    },
                                ]);
                            }}
                        />
                    }
                />
                <FormUtil.Record
                    textAlign="center"
                    isEnable={!(hasShrinkParam || hasDisappearParam)}
                    jsx={
                        <FormUtil.Switch
                            label={'Flash'}
                            width={300}
                            isUse={hasFlashParam}
                            callback={() => {
                                toggleElements(hasFlashParam, 5, [
                                    {
                                        name: ParamUtil.EffectParamType.FLASH_APPEAR_FRAME,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '1' }]
                                    },
                                    {
                                        name: ParamUtil.EffectParamType.FLASH_HIDE_FRAME,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '1' }]
                                    },
                                ]);
                            }}
                        />
                    }
                />
            </>}
        />
    </>);
}

export default EditorVisualMng;