import { type } from "os";
import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../../utils/form/validateUtil";
import TreeUtil from "../../../../../utils/treeUtil";
import ConfirmDialog from "../../../../dialog/confirmDialog";
import GloballDialog from "../../../../dialog/globalDialog";
import { GlobalContext } from "../../../../entry/systemEntry";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicElementUtil from "../../util/logicElementUtil";
import LogicUtil from "../../util/logicUtil";
import ModifyOperations from "../common/modifyOperations";

const EditorArgumentItem = () => {
    const { store, setStore } = useContext(GlobalContext);
    const { node, removeSelf, setModify, isModify, invalidate } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    const logicNode = node.parent?.parent as TreeUtil.ElementNode;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [argType, setArgType] = useState<FormUtil.CheckableValue>({ value: 'number', errors: [] });

    /**  */
    const reservedList = useMemo(() => {
        const owner = (node.parent?.parent?.data as LogicUtil.WrapElement).data;
        return LogicElementUtil.getParamNameList(owner);
    }, [node]);

    const mappingForm = () => {
        const param = wrap.data as LogicUtil.NodeArgumentItem;
        setName({ ...name, value: param.name });
        setArgType({ ...argType, value: param.valueType });
    };

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node]);

    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            checkableFields={[name, argType]}
            firstRegist={() => {
                const param: LogicUtil.NodeArgumentItem = {
                    name: name.value,
                    valueType: argType.value as LogicUtil.ArgumentType
                };
                param.name = name.value;
                wrap.data = param;
                syncArgumentAdd(logicNode, name.value);
            }}
            modifyRegist={() => {
                const param = wrap.data as LogicUtil.NodeArgumentItem;
                const prevName = param.name;
                param.name = name.value;
                param.valueType = argType.value as LogicUtil.ArgumentType;
                wrap.data = param;

                if (prevName !== name.value) {
                    syncArgumentName(logicNode, prevName, name.value);
                }
            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Name"
                    jsx={
                        <FormUtil.TextField
                            width={200}
                            isReadOnly={!isModify}
                            checkable={name}
                            setCheckable={setName}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 1, 16),
                                    errorType: "length"
                                },
                                {
                                    checker: (value) => LogicUtil.checkFieldName(value),
                                    errorType: "value"
                                },
                                {
                                    checker: (value) => {
                                        const list = reservedList.filter(name => {
                                            // 
                                            if (wrap.data != null) {
                                                return (wrap.data as LogicUtil.NodeArgumentItem).name !== name;
                                            }
                                            return true;
                                        });
                                        return !list.includes(value);
                                    },
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Type"
                    jsx={
                        <FormUtil.Combobox
                            checkable={argType}
                            setCheckable={setArgType}
                            list={LogicUtil.ArgTypes.map(type => {
                                return { value: type, labelText: type, isDisabled: type === 'color' }
                            })}
                            headBlank
                            width={200}
                            isReadOnly={!isModify}
                            validates={
                                [
                                    {
                                        checker: (value) => ValidateUtil.checkRequired(value),
                                        errorType: "required"
                                    }
                                ]
                            }
                        />
                    }
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.ButtonRecord
                    align="center"
                    buttons={[
                        {
                            label: 'Delete',
                            isEnable: !isModify,
                            callback: () => {
                                const param = wrap.data as LogicUtil.NodeArgumentItem;
                                const refers = getArgumentRefers(node, param.name);
                                if (refers.length === 0) {
                                    // DependInheritSend
                                    syncArgumentDel(logicNode, name.value);
                                    removeSelf();
                                } else {
                                    store.system.dialog = <ConfirmDialog.Component
                                        message={`[${param.name}] is referenced in the formula.\n${refers}`}
                                    />;
                                    setStore({ ...store });
                                }
                            }
                        }
                    ]}
                />
                {/*  */}
                {LogicElementUtil.getSwapButtons(node, invalidate)}
            </>}
        />
    </>);
}

export default EditorArgumentItem;

/**
 * 
 * @param node 
 * @param argName 
 * @returns 
 */
const getArgumentRefers = (node: TreeUtil.ElementNode, argName: string) => {

    const refers: string[] = [];

    const callback = (list: LogicUtil.WrapElement[]) => {
        list.forEach(wrap => {
            const param = wrap.data as LogicUtil.NodeParamItem;
            param.values.forEach(varItem => {
                if (varItem.type === 'argument' && varItem.value === argName) {
                    refers.push(param.name);
                }
            });
        });
    }
    LogicElementUtil.searchVariableReferInFormula(node, callback);

    return refers;
}

/**
* 
* @param logicNode 
*/
const syncArgumentName = (logicNode: TreeUtil.ElementNode, prevName: string, newName: string) => {

    const syncFormula = (list: LogicUtil.WrapElement[]) => {
        list.forEach(wrap => {
            const values = (wrap.data as LogicUtil.NodeParamItem).values;
            values.forEach(varItem => {
                if (varItem.type === 'argument' && varItem.value === prevName) {
                    varItem.value = newName;
                }
            });
        });
    }

    const wrap = logicNode.data as LogicUtil.WrapElement;
    const logicItem = wrap.data as LogicUtil.NodeLogicItem;
    const fieldMng = logicItem.mngs.find(mng => mng.type === 'field-mng');
    if (fieldMng != undefined) {
        syncFormula((fieldMng.data as LogicUtil.NodeFieldManager).fields);
    }
    const visualMng = logicItem.mngs.find(mng => mng.type === 'visual-mng');
    if (visualMng != undefined) {
        syncFormula((visualMng.data as LogicUtil.NodeParamManager).params);
    }
    const behaviorMng = logicItem.mngs.find(mng => mng.type === 'behavior-mng');
    if (behaviorMng != undefined) {
        syncFormula((behaviorMng.data as LogicUtil.NodeParamManager).params);
    }
    const inhMng = logicItem.mngs.find(mng => mng.type === 'inherit-mng');
    if (inhMng != undefined) {
        (inhMng.data as LogicUtil.NodeConnectManager).inherits.forEach(inh => {
            const inhfld = LogicElementUtil.getInnerWrap(inh, 'inhfld-mng');
            // InheritField
            if (inhfld != null) {
                syncFormula((inhfld.data as LogicUtil.NodeFieldManager).fields);
            }
            // Extend
            syncFormula((LogicElementUtil.getInnerWrap(inh, 'extend-mng')?.data as LogicUtil.NodeParamManager).params);
            // Send
            syncFormula((LogicElementUtil.getInnerWrap(inh, 'send-mng')?.data as LogicUtil.NodeParamManager).params);
        });
    }

    const syncParamName = (params: LogicUtil.WrapElement[]) => {
        params.forEach(wrap => {
            const item = wrap.data as LogicUtil.NodeParamItem;
            if (item.name === prevName) {
                item.name = newName;
            }
        });
    }
    if (wrap.type === 'logic-item') {

        // depend
        const runRec = (logic: LogicUtil.NodeLogicItem) => {
            const inhMng = logic.mngs.find(mng => mng.type === 'inherit-mng');
            if (inhMng == undefined) return;
            (inhMng.data as LogicUtil.NodeConnectManager).inherits.forEach(inh => {
                const conn = inh.data as LogicUtil.NodeInheritConnector;
                if (conn.method === 'create') {
                    const inhLogic = LogicElementUtil.getInnerWrapFixed(inh, 'inherit-logic').data as LogicUtil.NodeLogicItem;
                    runRec(inhLogic);
                } else if (conn.method === 'depend' && conn.depend === logicItem.name) {
                    const sendMng = LogicElementUtil.getInnerWrapFixed(inh, 'send-mng').data as LogicUtil.NodeParamManager;
                    syncParamName(sendMng.params);
                }
            });
        }
        // 
        const logicMngNode = logicNode.parent as TreeUtil.ElementNode;
        logicMngNode.children.forEach(logic => {
            runRec((logic.data as LogicUtil.WrapElement).data);
        });
        // 
        const rootNode = logicNode.parent?.parent as TreeUtil.ElementNode;
        const root = (rootNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeRoot;
        const defs = (root.seedMng.data as LogicUtil.NodeSeedManager).defs;
        console.log(defs.length);
        defs.forEach(wrap => {
            const interfaceWrap = LogicElementUtil.getInnerWrapFixed(wrap, 'interface-mng');
            const sendMng = LogicElementUtil.getInnerWrapFixed(interfaceWrap, 'seedsend-mng').data as LogicUtil.NodeSeedSendManager;
            syncParamName(sendMng.params);
        });
    } else if (wrap.type === 'inherit-logic') {
        const connWrap = logicNode.parent?.data as LogicUtil.WrapElement;
        const sendMng = LogicElementUtil.getInnerWrapFixed(connWrap, 'send-mng').data as LogicUtil.NodeParamManager;
        syncParamName(sendMng.params);
    }
}

/**
 * Sendcallback
 * @param logicNode 
 * @param callback 
 */
const syncArgumentCallback = (
    logicNode: TreeUtil.ElementNode,
    callback: (sendNode: TreeUtil.ElementNode) => void
) => {
    const wrap = logicNode.data as LogicUtil.WrapElement;
    const logicItem = wrap.data as LogicUtil.NodeLogicItem;

    if (wrap.type === 'logic-item') {
        const runRec = (logicNode: TreeUtil.ElementNode) => {
            const inhMngNode = logicNode.children.find(child => (child.data as LogicUtil.WrapElement).type === 'inherit-mng');
            if (inhMngNode == undefined) return;
            inhMngNode.children.forEach(child => {
                const conn = (child.data as LogicUtil.WrapElement).data as LogicUtil.NodeInheritConnector;
                if (conn.method === 'create') {
                    runRec(LogicElementUtil.getInnerNodeFixed(child, 'inherit-logic'));
                } else if (conn.method === 'depend' && conn.depend === logicItem.name) {
                    callback(LogicElementUtil.getInnerNodeFixed(child, 'send-mng'));
                }
            });
        }
        const logicMngNode = logicNode.parent as TreeUtil.ElementNode;
        logicMngNode.children.forEach(child => {
            runRec(child);
        });
        // 
        const rootNode = logicNode.parent?.parent as TreeUtil.ElementNode;
        rootNode.children[0].children.forEach(seedNode => {
            const interfaceNode = LogicElementUtil.getInnerNodeFixed(seedNode, 'interface-mng');
            const sendNode = LogicElementUtil.getInnerNodeFixed(interfaceNode, 'seedsend-mng');
            const seed = (seedNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeSeedItem;
            if (seed.entry === logicItem.name) {
                callback(sendNode);
            }
        });
    } else if (wrap.type === 'inherit-logic') {
        const sendNode = LogicElementUtil.getInnerNodeFixed(logicNode.parent as TreeUtil.ElementNode, 'send-mng');
        callback(sendNode);
    }
}

/**
 * Send
 * @param logicNode 
 */
const syncArgumentAdd = (logicNode: TreeUtil.ElementNode, newArgName: string) => {
    const addNewParam = (sendNode: TreeUtil.ElementNode) => {
        const paramWrap = LogicElementUtil.createParamWrap('send-item',
            { name: newArgName, formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] }
        );
        const sendMng = (sendNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamManager;
        sendMng.params.push(paramWrap);
        sendNode.children.push(TreeUtil.createNode(paramWrap, sendNode));
    }
    syncArgumentCallback(logicNode, addNewParam);
}

/**
 * Send
 * @param logicNode 
 */
const syncArgumentDel = (logicNode: TreeUtil.ElementNode, delArgName: string) => {
    const delArgParam = (sendNode: TreeUtil.ElementNode) => {
        const sendMng = (sendNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamManager;
        let delIndex = -1;
        sendMng.params.some((param, i) => {
            if ((param.data as LogicUtil.NodeParamItem).name == delArgName) {
                delIndex = i;
                return 1;
            }
        });
        sendMng.params.splice(delIndex, 1);
        sendNode.children.splice(delIndex, 1);
    }
    syncArgumentCallback(logicNode, delArgParam);
}