import { useContext, useEffect, useState } from "react";
import styled, { css } from "styled-components";
import Styles, { _CSS_LABEL_MIDIUM } from "../../base/design/styles";
import DataUtil from "../../utils/dataUtil";
import { HOME_USER_DIV_HEIGHT } from "../../utils/systemConst";
import TreeUtil from "../../utils/treeUtil";
import LogicElementUtil from "../create/factory/util/logicElementUtil";
import LogicUtil from "../create/factory/util/logicUtil";
import PreviewLogicFrame from "../create/preview/previewLogicFrame";
import ConfirmDialog from "../dialog/confirmDialog";
import { GlobalContext } from "../entry/systemEntry";

type LibProps = {
    name: string;
    ownerId: string;
    lastmod: string;
    rootData: LogicUtil.WrapElement;
    isOpen: boolean;
    seedList: SeedProps[];
}
type SeedProps = {
    name: string;
    disp?: string;
    argCount: number;
    master: LogicUtil.WrapElement;
}

const PublishFrame = (props: {
    setLibCnt: (artsCnt: number) => void;
}) => {

    const { store, setStore } = useContext(GlobalContext);

    const [libList, setLibList] = useState<LibProps[]>([]);

    useEffect(() => {
        findAllReleaseProjectList().then((resList) => {
            const libList: LibProps[] = [];
            resList.forEach(res => {
                const name = res.name;
                const ownerId = res.owner;
                const lastmod = res.lastmod;
                const rootData = JSON.parse(DataUtil.unZip(res.source)) as LogicUtil.WrapElement;
                const seedMng = (rootData.data as LogicUtil.NodeRoot).seedMng.data as LogicUtil.NodeSeedManager;
                const seedList: SeedProps[] = seedMng.defs
                    .filter(wrap => {
                        const seed = wrap.data as LogicUtil.NodeSeedItem;
                        return seed.permit === 'release';
                    })
                    .map(wrap => {
                        const seed = wrap.data as LogicUtil.NodeSeedItem;
                        const argMng = LogicElementUtil.getInnerWrapFixed(wrap, 'interface-mng', 'seedarg-mng');
                        const argCount = (argMng.data as LogicUtil.NodeSeedArgManager).args.length;
                        return { name: seed.name, disp: seed.disp, argCount, master: wrap };
                    });
                libList.push({
                    name, ownerId, lastmod, rootData, seedList, isOpen: false
                });
            });
            setLibList(libList);
            props.setLibCnt(libList.length);
        });
    }, []);

    const download = (plainData: string) => {
        const options = {
            types: [
                {
                    accept: {
                        'text/plain': ['.plgc'],
                    },
                },
            ],
        };
        window.showSaveFilePicker(options).then((handle) => {
            (async () => {
                // FileSystemWritableFileStream 
                const writable = await handle.createWritable();
                //
                const text = DataUtil.gZip(plainData);
                await writable.write(text);
                //
                await writable.close();
                store.system.dialog = <ConfirmDialog.Component message="Successfully download!" />;
                setStore({ ...store });
            })();
        }).catch(() => {
            console.log('');
        });
    }

    const libListJsx = libList
        .filter(lib => lib.seedList.length > 0).map((lib, i) => {
            const dispCnt = !lib.isOpen ? 0 : lib.seedList.length;
            return (<_Item key={i} count={dispCnt}>
                <_Header onClick={() => {
                    // window.open(DataUtil.getPreviewURL(score.seq), '_blank');
                }}>
                    <_Owner>{`${lib.ownerId}`}</_Owner>
                    <_CName onClick={() => {
                        download(JSON.stringify(lib.rootData));
                    }}>{`${lib.name}`}</_CName><_CDate>{lib.lastmod}</_CDate>
                </_Header>
                <_DetailMain count={dispCnt}>
                    {lib.seedList.map((seed, j) => {
                        let labelName = seed.name;
                        if (seed.disp != undefined) labelName = seed.disp;
                        return (
                            <_SeedItemFrame key={j} onClick={() => {
                                store.system.dialog = (
                                    <PreviewLogicFrame.Component
                                        master={seed.master}
                                        rootData={lib.rootData}
                                        closeButtonAction={() => {
                                            store.system.dialog = null;
                                            setStore({ ...store });
                                        }}
                                    />
                                );
                                setStore({ ...store });
                            }}>
                                <_SeedItemInner>
                                    <_SeedName>{labelName}</_SeedName>
                                    <_SeedArgCnt>{` [${seed.argCount} arguments]`}</_SeedArgCnt>
                                </_SeedItemInner>
                            </_SeedItemFrame>
                        );
                    })}
                </_DetailMain>
                <_DetailOpen onClick={() => {
                    libList[i].isOpen = !libList[i].isOpen;
                    setLibList(libList.slice());
                }}>
                    <_CSummary>{` ${lib.seedList.length} seeds `}</_CSummary>
                </_DetailOpen>
            </_Item>);
        });

    return (
        <_ListFrame>{libListJsx}</_ListFrame>
    );
}

export default PublishFrame;

export const findAllReleaseProjectList = async () => {
    const response = await DataUtil.sendQueryRequestToAPI('select', `
        SELECT name, seq, (select id from user_tbl where seq = prj.owner) as owner, source, lastmod
        FROM prjcttbl prj
        WHERE source <> ''
        ORDER BY lastmod desc
    `);
    const results = await response.json();
    return results as any[];
};

