import Particle from "../../graphic/particle";

namespace ModulateBuilder {

    type ModulateParam = {
        decayRate?: number;
        decayLimit?: number;
        gravity?: number;
        gravityLimit?: number;
    }

    export const build = (extendList: ((self: Particle.Instance) => void)[], param: ModulateParam) => {
        
        // 
        if (param.decayRate != undefined && param.decayLimit != undefined) {
            const decayLimit = param.decayLimit;
            const decayRate = param.decayRate;
            extendList.push((self: Particle.Instance) => {

                if (self.vec.len > decayLimit) {
                    self.vec.len = self.vec.len * decayRate;
                    self.vec.syncFromRad();
                }
            });
        }
        // 
        if (param.gravity != undefined && param.gravityLimit != undefined) {
            const gravityLimit = param.gravityLimit;
            const gravity = param.gravity;
            extendList.push((self: Particle.Instance) => {

                if (self.vec.xy.y < gravityLimit) {
                    self.vec.xy.y += gravity;
                    self.vec.syncFromXY();
                }
            });
        }
    }
}

export default ModulateBuilder;